<template>
<div>
    <div id="app" v-if="user && isLoaded">
      <navigation></navigation>
      <mobile-navigation></mobile-navigation>
      <header-module></header-module>
        <div class="app-view">
          <transition name="fading" mode="out-in">
            <router-view></router-view>
          </transition>
        </div>
      <modals></modals>
    </div>
    <div v-if="!user && isLoaded" class="auth-container">
      <router-view></router-view>
    </div>
</div>
</template>

<script>

import navigation from "@/components/desktop/navigation"
import mobileNavigation from "@/components/mobile/navigation"
import headerModule from "@/components/desktop/header"
import modals from "@/components/desktop/modals/modal"
import axios from "axios"
export default {
  name: 'app',
  components: {
    navigation, modals, mobileNavigation, headerModule
  },
  data: () => ({
    isLoaded: false
  }),
  created() {
    this.tokenValidation()
  },
  methods: {
    tokenValidation () {
      let token = JSON.parse(localStorage.getItem('token'))
      if(token) {
        axios.post("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/token/verify/", {
          token: JSON.parse(localStorage.getItem('token')).access || '123'
        }).then(data => {
          let token_access = token.access
          let token_refresh = token.refresh
          localStorage.setItem('token', JSON.stringify({access: token_access, refresh: token_refresh}))
          this.$store.dispatch('auth/SET_TOKEN', {access: token_access, refresh: token_refresh})
          this.auth()
        }).catch(err => {
          axios.post('https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/token/refresh/', {
            refresh: token.refresh
          }).then(tokenRefreshData => {
            let tokenPairs = {
              access: tokenRefreshData.data.access,
              refresh: token.refresh
            }
            
            let token_access = tokenPairs.access
            let token_refresh = tokenPairs.refresh
            localStorage.setItem('token', JSON.stringify({access: token_access, refresh: token_refresh}))
            this.$store.dispatch('auth/SET_TOKEN', {access: token_access, refresh: token_refresh})
            this.auth()
          })
        })
      } else {
        let registerRoutes = ['/auth', '/registration', '/registration/phone-verification', '/registration/password']
        console.log(this.$route.path)
        if(this.$route.path === '/auth') {
          // this.$router.push('/auth')
        } else if(this.$route.path !== registerRoutes.find(i => i === this.$route.path)) {
          this.$router.push('/auth')
        }
        this.isLoaded = true
      }
    },
    auth() {
      axios.get("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/students/me/categories/", {
      headers: {
        'Authorization': 'Bearer ' + this.token.access,
        'Content-Type': 'application/JSON'
      }
      }).then(categories => {
        this.$store.dispatch("common/ALL_CATEGORIES", categories.data.all)
        this.$store.dispatch("common/ALLOWED_CATEGORIES", categories.data.allowed)
        axios.get("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/students/me/", {
          params: {
            category: this.$store.getters["common/allowed_categories"][0] || 'b'
          },
          headers: {
            'Authorization': 'Bearer ' + this.token.access,
            'Content-Type': 'application/JSON'
          }
        }).then (data => {
          this.$store.dispatch('auth/SET_USER', data.data)
          // GETTING LESSONS 
          axios.get("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/lessons/", {
            params: {
              category: this.$store.getters["common/category"] || 'b'
            },
            headers: {
              'Authorization': 'Bearer ' + this.token.access,
              'Content-Type': 'application/JSON'
            }
          }).then(lessons => {
            this.isLoaded = true
            this.$store.dispatch('common/SET_LESSONS', lessons.data)
            this.$store.dispatch('common/SET_NEXT_LESSON', data.data.next_training_stage.context.lesson_id)
          })
        }) 
      })
    }
  },
  watch: {
    routerPath: function () {
      let registerRoutes = ['/auth', '/registration', '/registration/phone-verification', '/registration/password']
      if(this.routerPath !== registerRoutes.find(i => i === this.routerPath)) {
        this.tokenValidation()
      }
    }
  },
  computed: {
    token () {
      return this.$store.getters['auth/token']
    },
    user () {
      return this.$store.getters['auth/user']
    },
    routerPath() {
      return this.$route.path
    }
  },
  mounted () {

  }
}
</script>

<style lang="scss">
  .app-view {
    width: 100%;
    padding-left: 64px;
    box-sizing: border-box;
    background: #f0f1f7;
    min-height: calc(100vh - 64px);
    @media screen and (min-width: 1500px) {
      padding-left: 240px;
    }
    @media screen and (max-width: 600px) {
      padding-left: 0px;
      padding-bottom: 80px;
    }
  }
  .auth-container {
    min-height: 100vh;
    width: 100%;
    background: url("./assets/auth/background.png") no-repeat center center fixed;
    background-size: cover;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    & * {
      box-sizing: border-box;
    }
  }
  .fading-enter-active, .fading-leave-active {
    transition: opacity .2s;
  }
  .fading-enter, .fading-leave-to {
    opacity: 0;
  }
</style>
