<template>
  <div class="data">
      
    <div class="data-container">
      <div class="data-caption">
        <p class="data-caption-title"><span>Шаг 5: </span>Паспортные данные</p>
        <button @click="$router.push('/payment')" class="back"><img ondragstart="return false" src="../assets/payment/close.png" alt="">Отменить</button>
      </div>
      <div class="data-main">
        <div class="data-main-container">
          <div class="data-main-info">
            <span class="btn-info">i</span>
            <p class="text">Для офрмления онлайн договора внесите свои паспортные данные.</p>
          </div>
          <div class="data-main-wrapper">
            <div class="data-main-wrapper-row">
              <div class="data-main-wrapper-row-item">
                <p class="data-main-wrapper-row-item-title">Серия</p>
                <inputComponent :val="info.series" @onInput="onInputSeries" class="input-1"></inputComponent>
              </div>
              <div class="data-main-wrapper-row-item">
                <p class="data-main-wrapper-row-item-title">Номер</p>
                <inputComponent :val="info.number" @onInput="onInputNumber" class="input-1"></inputComponent>
              </div>
              <div class="data-main-wrapper-row-item">
                <p class="data-main-wrapper-row-item-title">Ф.И.О.</p>
                <inputComponent :val="info.name" @onInput="onInputName" class="input-2"></inputComponent>
              </div>
            </div>
            <div class="data-main-wrapper-row">
              <div class="data-main-wrapper-row-item">
                <p class="data-main-wrapper-row-item-title">Кем выдан</p>
                <inputComponent :val="info.issuedByWhom" @onInput="onInputIssuedByWhom" class="input-2"></inputComponent>
              </div>
              <div class="data-main-wrapper-row-item">
                <p class="data-main-wrapper-row-item-title">Когда выдан</p>
                <inputComponent :val="info.issuedDate" @onInput="onInputIssuedDate" class="input-3"></inputComponent>
              </div>
            </div>
            <div class="data-main-wrapper-row">
              <div class="data-main-wrapper-row-item">
                <p class="data-main-wrapper-row-item-title">Адрес прописки</p>
                <inputComponent :val="info.registrationAddress" @onInput="onInputRegistrationAdress" class="input-2"></inputComponent>
              </div>
            </div>
            <div class="data-main-wrapper-row data-main-wrapper-row-2">
              <div class="data-main-wrapper-row-item">
                <p class="data-main-wrapper-row-item-title">Фактический адрес проживания</p>
                <div class="input-wrapper">
                  <inputComponent :val="info.actualAdress" @onInput="onInputActualAdress" class="input-2"></inputComponent>
                  <label for="agreement-main-checkbox" class="agreement-main-checkbox">
                    <input v-model="isAdress" id="agreement-main-checkbox" type="checkbox">
                    <div :class="isAdress ? 'active' : ''" class="btn"><span></span></div>
                    <p class="text">Совпадает с адресом прописки</p>
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="data-main-btns">
          <button @click="back" class="back-btn">
            <img ondragstart="return false" src="../assets/payment/back.png" alt="">
            Назад 
          </button>
          <button v-show="isFilled" @click="next" class="next-btn">
            <img ondragstart="return false" src="../assets/dashboard-icons/next.png" alt="">
            Далее
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
 
import dropdown from "@/components/desktop/dropdown-component"
import inputComponent from "@/components/desktop/input-component"
export default {
  name: 'dataComponent',
  data: () => ({
    isAdress: false,
    info: {
      series: '',
      number: '',
      name: '',
      issuedByWhom: '',
      issuedDate: '',
      registrationAddress: '',
      actualAdress: ''
    }
  }),
  components: {
    dropdown, inputComponent
  },
  watch: {
    isAdress: function () {
      if(this.isAdress) {
        if(this.info.registrationAddress) {
          this.info.actualAdress = this.info.registrationAddress
        } else if (this.info.actualAdress) {
          this.info.registrationAddress = this.info.actualAdress
        } else {
          this.info.actualAdress = this.info.registrationAddress
        }
      } else {
        this.info.actualAdress = ''
      }
    }
  },
  computed: {
    isFilled() {
      let flag = true
      for(let i = 0; i < Object.values(this.info).length; i++) {
        if(Object.values(this.info)[i] === '') {
          flag = false
        }
      }
      return flag
    }
  },
  methods: {
    onInputSeries (e) {
      this.info.series = e
    },
    onInputNumber (e) {
      this.info.number = e
    },
    onInputName (e) {
      this.info.name = e
    },
    onInputIssuedByWhom (e) {
      this.info.issuedByWhom = e
    },
    onInputIssuedDate (e) {
      this.info.issuedDate = e
    },
    onInputRegistrationAdress (e) {
      this.info.registrationAddress = e
      if(this.isAdress) {
        this.info.registrationAddress = e
        this.info.actualAdress = e
      }
    },
    onInputActualAdress (e) {
      this.info.actualAdress = e
      if(this.isAdress) {
        this.info.registrationAddress = e
        this.info.actualAdress = e
      }
    },
    back() {
      history.back()
    },
    next () {
      this.$router.push("/payment/school/charge")
    }
  }
}
</script>
 
<style lang="scss" scoped>
  .data-container {
    padding: 40px;
    box-sizing: border-box;
    min-height: calc(100vh - 64px);
    height: 100%;
    display: flex;
    flex-direction: column;
    & * {
      box-sizing: border-box;
    }
    @media screen and (max-width: 1500px) {
      & {
        padding: 24px 24px 24px 24px;
      }
    }
    @media screen and (max-height: 960px) {
      & {
        padding: 24px;
      }
    }
  }
  .data-caption {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    &-title {
      margin: 0px 10px 0px 20px;
      font-family: 'Vox-Mit';
      color: #404151;
      font-size: 2.77rem;
      & span {
        color: #7b7c8f;
      }
    }
    & .back {
      padding: 12px 16px;
      background: #fff;
      color: #7b7c90;
      font-family: 'SSans-Reg';
      font-size: 1rem;
      border: none;
      box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
      border-radius: 6px;
      cursor: pointer;
      user-select: none;
      margin-left: auto;
      height: 40px;
      & img {
        margin-right: 8px;
        margin-bottom: -3px;
      }
    }
  }
  .data-main {
    flex: 1;
    width: 100%;
    background: #fff;
    margin-top: 25px;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
    &-container {
      flex: 1;
      background: linear-gradient(to bottom, #fff 93%, #f3f4fa 100%);
      border-bottom: 1px solid #dddfeb;
      padding: 16px;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
    }
    &-info {
      background: #f0f1f7;
      border-radius: 8px;
      width: 100%;
      padding: 15px;
      display: flex;
      align-items: flex-start;
      user-select: none;
      & .text {
        color: #404151;
        font-family: 'SSans-Reg';
        font-size: 1rem;
        margin: 0px;
      }
      & .btn-info {
        border-radius: 50%;
        max-width: 16px;
        width: 100%;
        height: 16px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;
        font-family: 'SSans-SB';
        cursor: pointer;
        font-size: 1rem;
        background: linear-gradient(to bottom, #ff6f2f, #f64628);
        padding-bottom: 1px;
        margin-right: 8px;
      }
    }
    &-wrapper {
      padding: 16px 24px 40px 24px;
      &-row {
        display: flex;
        align-items: flex-start;
        &-2 {
          margin-top: 24px;
          align-items: center;
          & .data-main-wrapper-row-item {
            margin-top: 0px;
          }
          & .input-wrapper {
            display: flex;
            align-items: center;
          }
        }
        & .agreement-main-checkbox {
          cursor: pointer;
          display: flex;
          align-items: center;
          user-select: none;
          margin-left: 24px;
          & input {
            display: none;
          }
          & .text {
            color: #404151;
            font-family: 'SSans-Reg';
            font-size: 1rem;
          }
          & .btn {
            width: 16px;
            height: 16px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid #dddfeb;
            margin-right: 8px;
            &.active {
              & span {
                background: #f44b2f;
              }
            }
            & span {
              width: 8px;
              height: 8px;
              background: transparent;
            }
          }
        }
        &-item {
          margin: 24px 24px 0px 0px;
          &-title {
            margin: 0px 0px 10px 20px; 
            color: #404151;
            font-size: 1rem;
            font-family: 'SSans-Reg';
          }
          & .input-1 {
            max-width: 130px;
            min-width: 130px;
          }
          & .input-2 {
            max-width: 600px;
            min-width: 600px;
          }
          & .input-3 {
            max-width: 285px;
            min-width: 285px;
          }
        }
      }
    }
    &-btns {
      padding: 24px 40px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      & .back-btn {
        background: #81909c;
        padding: 15px 30px;
        border-radius: 25px;
        border: none;
        outline: none;
        color: #fff;
        font-family: 'SSans-SB';
        font-size: 1.2rem;
        cursor: pointer;
        display: flex;
        align-items: center;
        margin-right: 20px;
        & img {
          margin-right: 10px;
        }
      }
      & .next-btn {
        background: linear-gradient(to bottom, rgb(255, 111, 47), #f64628);
        box-shadow: 0px 5px 11px 0px rgba(255, 111, 47, 0.3);
        padding: 15px 30px;
        border-radius: 25px;
        border: none;
        outline: none;
        color: #fff;
        font-family: 'SSans-SB';
        font-size: 1.2rem;
        cursor: pointer;
        display: flex;
        align-items: center;
        margin-right: 20px;
        & img {
          margin-right: 10px;
        }
        
      }
    }
  }
</style>
