export default {
  namespaced: true,
  state: {
    userData: {}
  },
  getters: {
    userData (state) {
      return state.userData
    }
  },
  mutations: {
    set_userData(state, payload) {
      state.userData[payload.key] = payload.value
    },
  },
  actions: {
    SET_USERDATA ({commit}, payload) {
      commit('set_userData', payload)
    }
  }
}