        <button @click="$router.push('/payment')" class="back"><img ondragstart="return false" src="../assets/payment/close.png" alt="">Отменить</button>
      </div>
      <div class="charge-main">
        <div class="charge-main-container charge-main-container-1">
          <p class="charge-caption-text">
            Ваш онлайн-договор доступен по <a target="_blank" href="https://www.google.ru/">ссылке</a>.
          </p>
          <p class="charge-caption-text">
            Чтобы договор вступил в силу, нужно внести <span>не меньше 12500 р.</span>
          </p>
        </div>
        <div class="charge-main-container charge-main-container-2">
          <div class="charge-main-container-row">
            <p class="charge-main-container-row-title">Введите промо-код</p>
            <div class="charge-main-container-row-flex">
              <inputComponent></inputComponent>
              <div class="charge-main-container-row-flex-accept">
                <img ondragstart="return false" src="../assets/payment/accept.png" alt="">
                <p class="charge-main-container-row-flex-accept-text">Применить</p>
              </div>
            </div>
          </div>
          <div class="charge-main-container-wrapper">
            <div class="charge-main-container-wrapper-price">
              <div class="charge-main-container-wrapper-price-item">
                <p class="charge-main-container-wrapper-price-item-title">Стоимость:</p>
                <p class="charge-main-container-wrapper-price-item-cost">300 <span>Р</span></p>
              </div>
              <div class="charge-main-container-wrapper-price-item">
                <p class="charge-main-container-wrapper-price-item-title">Скидка:</p>
                <p class="charge-main-container-wrapper-price-item-cost">-100 <span>Р</span></p>
              </div>
              <div class="charge-main-container-wrapper-price-item result">
                <p class="charge-main-container-wrapper-price-item-title">Итого к оплате:</p>
                <p class="charge-main-container-wrapper-price-item-cost">200 <span>Р</span></p>
              </div>
            </div>
          </div>
        </div>
        <div class="charge-main-btns">
          <button @click="back" class="back-btn">
            <img ondragstart="return false" src="../assets/payment/back.png" alt="">
            Назад 
          </button>
          <div class="charge-main-btns-pay">
            <div class="charge-main-btns-pay-item">
              <p class="charge-main-btns-pay-item-text">
                Сумма оплаты:
              </p>
              <inputComponent class="input"></inputComponent>
            </div>
            <button @click="next" class="next-btn">
              <img ondragstart="return false" src="../assets/payment/card-icon.png" alt="">
              Оплатить и завершить
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
 
import dropdown from "@/components/desktop/dropdown-component"
import inputComponent from "@/components/desktop/input-component"
export default {
  name: 'charge',
  data: () => ({
    isAdress: false,
    info: {
      series: '',
      number: '',
      name: '',
      issuedByWhom: '',
      issuedDate: '',
      registrationAddress: '',
      actualAdress: ''
    }
  }),
  components: {
    dropdown, inputComponent
  },
  watch: {
    isAdress: function () {
      if(this.isAdress) {
        if(this.info.registrationAddress) {
          this.info.actualAdress = this.info.registrationAddress
        } else if (this.info.actualAdress) {
          this.info.registrationAddress = this.info.actualAdress
        } else {
          this.info.actualAdress = this.info.registrationAddress
        }
      } else {
        this.info.actualAdress = ''
      }
    }
  },
  computed: {
    isFilled() {
      let flag = true
      for(let i = 0; i < Object.values(this.info).length; i++) {
        if(Object.values(this.info)[i] === '') {
          flag = false
        }
      }
      return flag
    }
  },
  methods: {
    onInputSeries (e) {
      this.info.series = e
    },
    onInputNumber (e) {
      this.info.number = e
    },
    onInputName (e) {
      this.info.name = e
    },
    onInputIssuedByWhom (e) {
      this.info.issuedByWhom = e
    },
    onInputIssuedDate (e) {
      this.info.issuedDate = e
    },
    onInputRegistrationAdress (e) {
      this.info.registrationAddress = e
      if(this.isAdress) {
        this.info.registrationAddress = e
        this.info.actualAdress = e
      }
    },
    onInputActualAdress (e) {
      this.info.actualAdress = e
      if(this.isAdress) {
        this.info.registrationAddress = e
        this.info.actualAdress = e
      }
    },
    back() {
      history.back()
    },
    next () {
      this.$router.push("/payment/school/charge")
    }
  }
}
</script>
 
