<template>
  <div class="input">
    <label class="current-value">
      <input v-model="value" @input="onInput" class="current-value-title" placeholder="—">
    </label>
    <div v-if="inputClass === 'extradots'" class="extradots">
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>
</template>

<script>

export default {
  name: 'input-component',
  props: ["val", "inputClass"],
  data: () => ({
    value: ''
  }),
  methods: {
    onInput () {
      this.$emit("onInput", this.value)
    }
  },
  computed: {
    
  },
  watch: {
    val: function () {
      this.value = this.val
    }
  }
}
</script>

<style lang="scss" scoped>
  .input {
    background: #fff;
    max-width: 280px;
    min-width: 280px;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    user-select: none;
    cursor: pointer;
    position: relative;
    &.active {
      border-bottom: none;
      border-bottom-left-radius: 0px;
      border-bottom-right-radius: 0px;
      margin-bottom: 1px;
      & .current-value {
        &-title {
          color: #404151;
        }
      }
    }
    & .extradots {
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translate(0, -50%);
      display: flex;
      align-items: center;
      cursor: text;
      & span {
        widtH: 2px;
        height: 2px;
        background: #f44b2f;
        margin: 0px 1px;
        display: block;
      }
    }
    & * {
      box-sizing: border-box;
    }
    & .current-value {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      &-title {
        margin: 0px;
        color: #7b7c90;
        font-family: 'SSans-Reg';
        font-size: 1.2rem;
        transition-duration: .2s;
        border: none;
        width: 100%;
        padding: 15px 20px;
        border: 1px solid #dddfeb;
        border-radius: 25px;
        position: relative;
      }
    }
  }
</style>