<template>
  <div class="test-component">
    <div class="test-component-caption">
      <div :class="`${questionNumb.status} ${idx === currentQuestion ? 'current' : ''}`" @click="changeQuestion(idx)" v-for="(questionNumb, idx) in testData" :key="idx" class="item">
        {{ idx + 1 }}
      </div>
    </div>
    <question v-show="idx === currentQuestion" v-for="(q, idx) in testData" :key="idx" @answer="answer" :questionData="questionDataMeth(idx)"></question>
    <div ref='qBtn' class="test-component-next">
      <button @click="nextQ" class="test-component-next-btn"><img ondragstart="return false" src="../../../assets/dashboard-icons/next.png" alt="">Далее</button>
    </div>
  </div>
</template>

<script>
import question from "@/components/desktop/test/question"
export default {
  name: "test-component",
  components: {
    question
  },
  props: ['testData'],
  data: () => ({
    currentQuestion: 0,
  }),
  methods: {
    questionDataMeth (idx) {
      let questionData = this.testData[idx]
      return {data: questionData, idx: idx}
    },
    changeQuestion (idx) {
      this.currentQuestion = idx
    },
    nextQ () {
      if(this.testData.length - 1 > this.currentQuestion) {
        this.currentQuestion = ++this.currentQuestion
      }
    },
    answer(q) {
      setTimeout(() => {
        this.$refs.qBtn.scrollIntoView({
          behavior: 'smooth',
          block: 'start'
        })
      }, 1);
      this.$emit('answer', q)
    }
  },
}
</script>

<style lang="scss">
  .test-component {
    width: 100%;
    background: #fff;
    border-radius: 7px;
    box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
    box-sizing: border-box;
    &-caption {
      width: 100%;
      border-top-left-radius: 7px;
      border-top-right-radius: 7px;
      padding: 8px 13px 9px 13px;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      background: linear-gradient(to bottom, #fff 50%, #f3f4fa 150%);
      border-bottom: 1px solid #dddfeb;
      box-sizing: border-box;
      & .item {
        width: 40px;
        height: 40px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: transparent;
        border-width: 2px;
        border-style: solid;
        color: #404151;
        font-size: 1.2rem;
        font-family: 'SSans-Reg';
        border-color: transparent;
        user-select: none;
        cursor: pointer;
        box-sizing: border-box;
        margin: 8px 4px;
        background: linear-gradient(to bottom, #fff 50%, #f3f4fa 150%);
        &.true {
          border-color: #2fc916;
        }
        &.false {
          border-color: #dc3457;
        }
        &.new {
          border-color: #dddfeb;
          color: #7b7c8f;
        }
        &.current {
          border-color: #7b7c8f;
          color: #404151;
          border-top-width: 10px;
          border-bottom-width: 10px;
          margin: 0px 4px;
          height: 56px;
          font-family: 'SSans-SB';
          &.true {
            border-color: #2fc916;
          }
          &.false {
            border-color: #dc3457;
          }
        }
      }
    }
    &-next {
      padding: 12px 24px;
      @media screen and (max-height: 875px) {
        & {
          padding: 6px 12px;
        }
      }
      &-btn {
        background: linear-gradient(to bottom, rgb(255, 111, 47), #f64628);
        padding: 14px 32px 14px 24px;
        border-radius: 25px;
        border: none;
        outline: none;
        color: #fff;
        font-family: 'SSans-SB';
        font-size: 1.2rem;
        cursor: pointer;
        box-shadow: 0px 5px 11px 0px rgba(255, 111, 47, 0.3);
        display: flex;
        align-items: center;
        & img {
          margin-right: 10px;
        }
      }
    }
  }
</style>