<template>
  <div id="dashboard">
    <div class="desktop">
        
      <div class="dashboard-container">
        <div class="row">
          <card-next-step></card-next-step>
          <card-schedule></card-schedule>
          <card-statistic></card-statistic>
        </div>
        <div v-if="category" class="row band-row">
          <band></band>
        </div>
      </div>
    </div>
    <div class="mobile">
      <mobile-header-component></mobile-header-component>
      <mobile-next-step-card></mobile-next-step-card>
      <mobile-card-shedule></mobile-card-shedule>
    </div>
  </div>
</template>

<script>
// Desktop
 
import cardNextStep from "@/components/desktop/dashboard/cardNextStep"
import cardSchedule from "@/components/desktop/dashboard/cardSchedule"
import cardStatistic from "@/components/desktop/dashboard/cardStatistic"
import band from "@/components/desktop/dashboard/band"

// Mobile
import mobileHeaderComponent from "@/components/mobile/header/index"
import mobileNextStepCard from "@/components/mobile/dashboard/mobileNextStepCard"
import mobileCardShedule from "@/components/mobile/dashboard/mobileCardShedule"

export default {
  name: 'dashboard',
  components: {
    cardNextStep, cardSchedule, cardStatistic, band,
    mobileHeaderComponent, mobileNextStepCard, mobileCardShedule
  },
  computed: {
    category() {
      return this.$store.getters["common/category"]
    }
  }
}
</script>

<style lang="scss">
  .dashboard-container {
    height: 100% !important;
    min-height: calc(100vh - 64px);
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    & .band-row {
      margin: auto 0px;
    }
    & .row {
      padding: 40px 40px 0px 40px;
      display: flex;
      height: 100%;
      & {
        height: 100% !important;
      }
      @media screen and (max-width: 1500px) {
        & {
          padding: 24px 24px 0px 24px;
        }
      }
      @media screen and (max-width: 890px) and (min-width: 550px) {
        & {
          flex-wrap: wrap;
        }
      }
    }
  }
  @media screen and (min-width: 600px) {
    #dashboard .mobile {
      display: none;
    }
    #dashboard .desktop {
      display: block;
    }
  }
  @media screen and (max-width: 600px) {
    #dashboard .mobile {
      display: block;
      overflow: hidden;
    }
    #dashboard .desktop {
      display: none;
    }
  }
</style>