<template>
  <div class="notifications-page">
      
    <div class="notifications-page-container">
      <div class="notifications-page-caption">
        <p class="notifications-page-caption-title">Уведомления <span v-if="newElements > 0">{{ newElements }} {{ wordsTransitirate }}</span></p>
      </div>
      <div class="notifications-page-wrapper">
        <label :class="notifications[idx].isActive ? '' : 'new'" :for="idx" v-for="(n, idx) in notifications" :key="n.at" class="notifications-page-wrapper-item">
          <div class="notifications-page-wrapper-item-text">
            <p class="notifications-page-wrapper-item-text-at">{{ n.at }}</p>
            <p class="notifications-page-wrapper-item-text-text">{{ n.text }}</p>
          </div>
          <input v-model="notifications[idx].isActive" :id="idx" type="checkbox">
          <div :class="notifications[idx].isActive ? 'active' : ''" class="checkbox"><span></span></div>
        </label>
      </div>
    </div>
    <div v-if="choosenElements > 0" class="extraModal">
      <img class="icon" ondragstart="return false" src="../assets/settings/delete-icon.png" alt="">
      <p class="extraModal-text">Удалить {{ choosenElements }} {{ wordsTransitirate2 }}</p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'notifications-page',
  components: {
      
  },
  data: () => ({
    notifications: [
      {
        at: 'Сегодня в 15:00',
        text: 'На следующей неделе все занятия по вождению переносятся на неделю вперед. Свяжитесь со своим инструктором для уточнения даты и времени обучения. Связаться можно по телефону в разделе “Расписание“.',
        isActive: false
      },
      {
        at: 'Вчера в 12:45',
        text: 'Просьба закрыть долг по оплате за вождение (2 платежа с начала этого года). Полные расчеты доступны в разделе “Оплата”.',
        isActive: false
      },
      {
        at: '23 марта 9:00',
        text: 'У вас 2 незакрытых платежа за март. Ознакомиться с балансом и оплатить долг можно в разделе “Оплата”.',
        isActive: false
      }
    ]
  }),
  methods: {
    
  },
  computed: { 
    newElements () {
      return this.notifications.filter(n => !n.isActive) ? this.notifications.filter(n => !n.isActive).length : 0
    },
    choosenElements () {
      return this.notifications.filter(n => n.isActive) ? this.notifications.filter(n => n.isActive).length : 0
    },
    wordsTransitirate () {
      if(this.newElements === 1) {
        return 'непрочитанное'
      } else if (this.newElements >= 2) {
        return 'непрочитанных'
      }
    },
    wordsTransitirate2 () {
      if(this.choosenElements === 1) {
        return 'выбранное уведомление'
      } else if (this.choosenElements >= 2) {
        return 'выбранных уведомлений'
      }
    }
  }
}
</script>
 
<style lang="scss">
  .extraModal {
    position: fixed;
    bottom: 0px;
    width: 100vw;
    padding: 25px 10px;
    left: 0px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    cursor: pointer;
    background: linear-gradient(to bottom, #fff 50%, #f3f4fa 100%);
    & .icon {
      margin-right: 10px;
    }
    &-text {
      font-family: 'SSans-Reg';
      font-size: 1rem;
      color: #7b7c90;
      margin: 0px;
    }
  }
  #navigation .apps {
    display: none;
  }
  .notifications-page-container {
    padding: 40px;
    box-sizing: border-box;
    min-height: calc(100vh - 64px);
    height: 100%;
    display: flex;
    flex-direction: column;
    & * {
      box-sizing: border-box;
    }
    @media screen and (max-width: 1500px) {
      & {
        padding: 24px 24px 24px 24px;
      }
    }
    @media screen and (max-height: 960px) {
      & {
        padding: 24px;
      }
    }
  }
  .notifications-page-caption {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    flex-wrap: wrap;
    &-title {
      margin: 0px 10px 0px 20px;
      font-family: 'Vox-Mit';
      color: #404151;
      font-size: 2.77rem;
      & span {
        color: #7b7c8f;
        font-size: 1.6rem;
        margin-left: 20px;
      }
    }

  }
  .notifications-page-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    &-item {
      width: 100%;
      user-select: none;
      cursor: pointer;
      display: flex;
      align-items: center;
      padding: 40px 30px 26px 40px;
      border-radius: 8px;
      box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
      margin: 4px 0px;
      background: #fff;
      position: relative;
      &.new {
        &:before {
          content: '';
          position: absolute;
          left: 8px;
          top: 8px;
          width: 12px;
          height: 12px;
          display: block;
          border-radius: 50%;
          background: linear-gradient(to bottom, rgb(255, 111, 47), #f64628);
        }
        & .notifications-page-wrapper-item-text-text {
          font-family: 'SSans-SB';
        }
      } 
      &-text {
        &-at {
          color: #7b7c90;
          font-family: 'SSans-Reg';
          font-size: 1.2rem;
          margin: 0px 0px 20px 0px;
        }
        &-text {
          color: #404151;
          font-family: 'SSans-Reg';
          font-size: 1.2rem;
        }
      }
      & input {
        display: none;
      }
      & .checkbox {
        min-width: 16px;
        min-height: 16px;
        max-width: 16px;
        max-height: 16px;
        border: 1px solid #dddfeb;
        margin-left: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        &.active span {
          background: #f44b2f;
          min-width: 8px;
          min-height: 8px;
          max-width: 8px;
          max-height: 8px;
        }
      }
    }
  }
</style>
