<template>
  <div class="tickets-items">
    <div @click="selectTicket(i)" v-for="(i, idx) in tickets" :key="idx" class="item" :class="isItemActive(i)">
      <div class="counter">
        <span>{{ idx + 1 }}</span>
      </div>
      <div v-if="i.hasResult" class="text">
        <p class="true-count">Правильно: {{ i.num_rights }}</p>
        <p class="false-count">Ошибок: {{ i.num_errors }}</p>
        <!-- <p class="default">Билет не пройден</p> -->
      </div>
      <div v-else class="text">
        <p class="default">Билет <br/> не пройден</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "tickets-items",
  props: ['tickets'],
  data: () => ({
    // tickets: [
    //   {
    //     true: 6,
    //     false: 14,
    //     isPassed: false,
    //     isTried: true
    //   },
    // ]
  }),
  methods: {
    isItemActive (i) {
      let res = null
      if(i.has_result && i.is_successful) {
        res = 'true'
      } else if(i.has_result && !i.is_successful) {
        res = 'false'
      } if(!i.has_result && !i.is_successful) {
        res = ''
      }
      return res
    },
    selectTicket (i) {
      this.$router.push({
        path: `/dashboard/training/${i.number}`,
        // query: {ticket_number: i.number}
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .tickets-items {
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    // justify-content: space-between;
    margin: 19px 0px -5px 0px;
    box-sizing: border-box;
    & * {
      box-sizing: border-box;
    }
    & .item {
      width: calc(((100% / 10) * 2) - 8px);
      display: flex;
      align-items: center;
      margin: 4px;
      box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
      background: linear-gradient(to bottom, #fff 50%, #f3f4fa 150%);
      border-radius: 8px;
      border: 1px solid transparent;
      user-select: none;
      position: relative;
      cursor: pointer;
      &.false {
        border-color: #dc3457;
        &:after {
          content: '';
          width: 12px;
          height: 12px;
          position: absolute;
          right: 5px;
          bottom: 5px;
          background: url("../../../assets/stats-icons/false-small.png");
        }
        & .text {
          & .false-count {
            color: #dc3457;
          }
        }
        & .counter {
          & span {
            color: #3f434f;
          }
        }
      }
      &.true {
        border-color: #2fc916;
        &:after {
          content: '';
          width: 18px;
          height: 12px;
          position: absolute;
          right: 5px;
          bottom: 5px;
          background: url("../../../assets/stats-icons/true-small.png");
        }
        & .counter {
          & span {
            color: #3f434f;
          }
        }
      }
      & .counter {
        width: 52px;
        height: 52px;
        border-right: 1px solid #dddfeb;
        position: relative;
        & span {
          color: #7b7c8f;
          font-size: 1.84615rem;
          font-family: 'Vox-Mit';
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          position: absolute;
        }
      }
      & .text {
        margin-left: 12px; 
        & .true-count {
          margin: 0px;
          font-family: 'SSans-Reg';
          font-size: .8rem;
          color: #7b7c8f;
        }
        & .false-count {
          margin: 0px;
          font-family: 'SSans-Reg';
          font-size: .8rem;
          color: #404151;
        }
        & .default {
          margin: 0px;
          color: #7b7c8f;
          font-size: .8rem;
          font-family: 'SSans-Reg';
        }
      }
    }
  }
</style>