<template>
  <div class="question-test">
    <div class="question-test-container">
      <p class="title">Вопрос {{ questionData.idx + 1 }}</p>
      <img class="preview" ondragstart="return false" :src="questionData.data.img" alt="">
      <p class="question">{{ questionData.data.question }}</p>
      <div class="answers">
        <button :disabled="isAnswered" :class="currentAnsw === idx ? 'answered' : ''" @click="chooseAnsw(answ, idx)" v-for="(answ, idx) in questionData.data.answers" :key="answ.text" class="answers-item">
          <span>{{ idx + 1}}.</span>
          <p class="text">{{ answ.text }}</p>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'question-test',
  props: ['questionData'],
  data: () => ({
    currentAnsw: null
  }),
  computed: {
    isAnswered() {
      return this.questionData.data.isAnswered ? this.questionData.data.isAnswered.isAnswered : false
    }
  },
  methods: {
    // toAnswer (a) {
    //   let q = this.questionData
    //   q['isAnswered'] = {isAnswered: true, isCorrect: a.isCorrect}
    //   this.$emit('answer', {q, a})
    //   if(!a.isCorrect) {
    //     // e.currentTarget.classList.add('false')
      
    //   }
    // }
    chooseAnsw(a, idx) {
      this.currentAnsw = idx
      let q = this.questionData
      this.$emit('chooseAnsw', {q, a})
    }
  }
}
</script>

<style lang="scss" scoped>
  .question-test {
    width: 100%;
    box-sizing: border-box;
    background: linear-gradient(to bottom, #fff 95%, #f3f4fa 100%);
    border-bottom: 1px solid #dddfeb;
  }
  .question-test-container {
    padding: 24px;
    width: 100%;
    box-sizing: border-box;
    @media screen and (max-height: 875px) {
      & {
        padding: 12px;
      }
    }
  }
  .title {
    font-family: "Vox-Mit";
    font-size: 2.15rem;
    color: #404151;
    margin: 0px;
  }
  .preview {
    margin: 18px 0px 23px 0px;
    max-width: 100%;
    max-height: 250px;
    @media screen and (max-height: 960px) {
      & {
        max-height: 210px;
      }
    }
  }
  .question {
    font-family: 'SSans-Reg';
    font-size: 1.2rem;
    color: #404151;
    margin: 0px 0px 14px 0px;
  }
  .answers {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    &-item {
      display: flex;
      align-items: flex-start;
      padding: 14px 16px 10px 16px;
      border-radius: 4px;
      border: 2px solid #dddfeb; 
      user-select: none;
      margin: 4px 0px;
      cursor: pointer;
      box-sizing: border-box;
      background: transparent;
      &.answered {
        border-color: #7b7c8f;
      }
      & span {
        display: block;
        margin-right: 5px;
        font-family: "SSans-SB";
        font-size: 1.2rem;
        color: #404151;
      }
      & .text {
        margin: 0px;
        font-size: 1.2rem;
        font-family: 'SSans-Reg';
        color: #404151;
        text-align: left;
      }
    }
  }
  .extra-info {
    display: flex;
    align-items: flex-start;
    padding: 23px 16px 17px 16px;
    background: #f0f1f7;
    border-radius: 4px;
    margin-top: 4px;
    & .desc {
      margin: 0px;
      width: 100%;
      color: #404151;
      font-family: 'SSans-Reg';
      font-size: 1rem;
      margin-top: -1px;
    }
    & .round-icon {
      border-radius: 50%;
      width: 16px;
      height: 16px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      color: #fff;
      font-family: 'SSans-SB';
      cursor: pointer;
      font-size: 1rem;
      background: linear-gradient(to bottom, #ff6f2f, #f64628);
      margin-right: 9px;
      box-sizing: border-box;
    }
  }
</style>