export default {
  namespaced: true,
  state: {
    category: null,
    allowed_categories: null,
    all_categories: null,
    lessons: null,
    next_lesson: null
  },
  getters: {
    category(state) {
      return state.category
    },
    allowed_categories(state) {
      return state.allowed_categories
    },
    all_categories(state) {
      return state.all_categories
    },
    lessons(state) {
      return state.lessons
    },
    next_lesson(state) {
      return state.next_lesson
    },
  },
  mutations: {
    set_category(state, payload) {
      state.category = payload ? payload : 'b'
    },
    allowed_categories(state, payload) {
      state.allowed_categories = payload ? payload : ['b']
    },
    all_categories(state, payload) {
      state.all_categories = payload
    },
    set_lessons(state, payload) {
      state.lessons = payload
    },
    set_next_lesson(state, payload) {
      state.next_lesson = state.lessons.find(i => i.id === payload)
    }
  },
  actions: {
    SET_CATEGORY({ commit }, payload) {
      commit('set_category', payload)
    },
    ALLOWED_CATEGORIES({ commit }, payload) {
      commit('allowed_categories', payload)
      commit('set_category', payload[0])
    },
    ALL_CATEGORIES({ commit }, payload) {
      commit('all_categories', payload)
    },
    SET_LESSONS({ commit }, payload) {
      commit('set_lessons', payload)
    },
    SET_NEXT_LESSON({ commit }, payload) {
      commit('set_next_lesson', payload)
    },
  }
}