<template>
<div class="headerModule">
  <div id="header">
    <div class="header-wrapper">
      <div class="account-info">
        <div class="logo">
          <img ondragstart="return false" src="../../../assets/header-icons/logo.png" alt="">
        </div>
        <div class="account-main-info">
          <div class="user-avatar">
            <img v-if="user.avatar" ondragstart="return false" src="../../../assets/header-icons/avatar.png" alt="">
            <img v-else ondragstart="return false" src="https://yt3.ggpht.com/a/AGF-l7-aze1MpQWcwg6i7exQYZHYdD1hxBG_DeHYXw=s900-c-k-c0xffffffff-no-rj-mo" alt="">
          </div>
          <p class="username">{{ user.first_name }}</p>
        </div>
        <div class="myid" v-if="user.id">
          <p class="myid-title">Мой ID: <span>{{ user.id }}</span></p>
        </div>
        <div class="mygroup" v-if="user.group.number && false">
          <p class="mygroup-title">Моя группа: <span>{{ user.group }}</span></p>
        </div>
        <div class="categories">
          <p class="categories-title">Категория:</p>
          <div class="items">
            <div @click="c.isAllowed ? categoryChanger(c) : false" v-for="c in categories" :key="c" class="item" :class="categoryClass(c)">
              <span>{{ c }}</span>
            </div>
          </div>
          <div @click="isCategoryChangerOpen = !isCategoryChangerOpen" class="category-changer" :class="isCategoryChangerOpen ? 'opened' : ''">
            <span class="current-category">{{ currentCategory }}</span>
            <div class="extra" v-show="isCategoryChangerOpen">
              <span @click="c.isAllowed ? categoryChanger(c.title) : false" :class="c.isAllowed ? 'category-allowed' : 'category-notallowed'" v-for="c in filteredCategories" :key="c.title" class='category'>
                {{ c.title }}
              </span>
            </div>
            <img ondragstart="return false" class="category-arrow" src="../../../assets/header-icons/right-arrow.svg" alt="">
          </div>
        </div>
      </div>
      <div class="tech-info" :class="notifications.isOpen ? 'tech-active-notify' : ''">
        <div v-click-outside="hideNotifications" class="notifications" :class="notifications.isOpen ? 'active' : ''" @click="openNotifications">
          <div class="icon">
            <img ondragstart="return false" src="../../../assets/header-icons/notifications-icon.svg" alt="">
          </div>
          <p class="tech-info-title">
            Уведомления
          </p>
          <div class="round-amount"><span>{{ notifications.amount }}</span></div>
          <div v-if="notifications.isOpen" class="list">
            <div class="item" :class="item.isNewMsgs ? 'new' : ''" v-for="item in notifications.items" :key="item.title">
              <span v-if="item.isNewMsgs"></span>
              {{item.title}}
            </div>
            <div @click="notification_showMore" class="showMore">
              <img ondragstart="return false" src="../../../assets/header-icons/plus.png" alt="">
              Показать все
            </div>
          </div>
        </div>
        <div @click="isMsgsOpen = true" class="messages">
          <div class="icon">
            <img ondragstart="return false" src="../../../assets/header-icons/messages-icon.png" alt="">
          </div>
          <p class="tech-info-title">
            Сообщения 
          </p>
          <div class="round-amount"><span>2</span></div>
        </div>
        <div @click="quit" class="quit">
          <img ondragstart="return false" src="../../../assets/header-icons/quit-icon.png" alt="">
        </div>
      </div>
    </div>
  </div>
  <transition name="messages-fade" mode="out-in">
    <messages :isMsgsOpen="isMsgsOpen" v-show="isMsgsOpen" @closeMessages="closeMessages"></messages>
  </transition>
  <div @click="isCategoryChangerOpen = false" v-show="isCategoryChangerOpen" class="blur-background"></div>
</div>
</template>


<script>
import messages from "@/components/desktop/header/messages"
import ClickOutside from 'vue-click-outside'
import axios from "axios"
export default {
  name: 'headerComponent',
  components: {
    messages
  },
  directives: {
    ClickOutside
  },
  data: () => ({
    isMsgsOpen: false,
    notifications: {
      isOpen: false,
      amount: 45,
      items: [
        {
          title: 'Сообщение от администратора',
          isNewMsgs: false
        },
        {
          title: 'Просроченная оплата',
          isNewMsgs: true
        },
        {
          title: 'Поздравляем с записью в автошколу',
          isNewMsgs: true
        },
        {
          title: 'Обновленные тарифы',
          isNewMsgs: true
        },
        {
          title: 'Уведомление о завершении курса',
          isNewMsgs: false
        },
        {
          title: 'Курс скоро завершается',
          isNewMsgs: false
        },
        {
          title: 'Нужно продлить курс',
          isNewMsgs: false
        },
        {
          title: 'Технические детали подписки',
          isNewMsgs: false
        }
      ]
    },
    isCategoryChangerOpen: false,
    currentCategory: '',
    categories: null
  }),
  mounted() {
    this.categories = this.all_categories
    this.currentCategory = this.current_category
  },
  methods: {
    quit() {
      this.$store.dispatch('auth/QUIT_USER') 
      this.$router.push('/auth')
    },
    closeMessages() {
      this.isMsgsOpen = false
    },
    categoryClass (c) {
      let result = 'disabled'
      if(this.allowed_categories && this.currentCategory) {
        if(this.currentCategory.toUpperCase() === c.toUpperCase()) {
          result = 'active'
        } else if(this.user.allowed_categories.find(i => i.toUpperCase() === c.toUpperCase())) {
          result = ''
        }
      }
      return result
    },
    openNotifications() {
      this.notifications.isOpen = !this.notifications.isOpen
    },
    hideNotifications() {
      this.notifications.isOpen = false
    },
    categoryChanger (e) {
      this.currentCategory = e
      setTimeout(() => {
        this.isCategoryChangerOpen = false
      }, 0);
      this.$store.dispatch("common/SET_CATEGORY", this.currentCategory)
    },
    categoryChangerHideOrShow() {
      setTimeout(() => {
        this.isCategoryChangerOpen = !this.isCategoryChangerOpen
      }, 0);
    },
    notification_showMore() {
      this.$router.push("/notifications")
    }
  },
  computed: {
    filteredCategories () {
      // let categories = []
      // for(let i = 0; i < this.categories.length; i++) {
      //   if(this.categories[i].title !== this.currentCategory) {
      //     categories.push(this.categories[i])
      //   }
      // }
      // return categories
      return 1
    },
    user() {
      return this.$store.getters['auth/user']
    },
    token () {
      return this.$store.getters['auth/token']
    },
    all_categories() {
      return this.$store.getters['common/all_categories']
    },
    allowed_categories() {
      return this.$store.getters['common/allowed_categories']
    },
    current_category() {
      return this.$store.getters['common/category']
    }
  },
  watch: {
    all_categories: function () {
      this.categories = this.all_categories
    },
    current_category: function () {
      this.currentCategory = this.current_category
    },
    allowed_categories: function () {
      // this.categories = this.allowed_categories
    },
  }
}
</script>


<style lang="scss">
  .messages-fade-enter-active, .messages-fade-leave-active {
    transition: opacity .3s;
  }
  .messages-fade-enter, .messages-fade-leave-to {
    opacity: 0;
  }
  .headerModule {
    padding-left: 240px;
    @media screen and (max-width: 1500px) {
      padding-left: 64px;
    }
    @media screen and (max-width: 600px) {
      display: none;
    }
  }
  .header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    background: linear-gradient(to bottom, #fff 50%, #f2f4fa 150%);
    box-shadow: 0px 1px 3px 0px rgba(228, 229, 236, 1);
    user-select: none;
  }
  .account-info {
    display: flex;
    align-items: center;
  }
  .logo {
    width: 64px;
    height: 64px;
    border-right: 1px solid #dddfeb;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    display: none;
    @media screen and (max-width: 1500px) {
      & {
        display: flex;
      }
    }
  }
  .account-main-info {
    display: flex;
    align-items: center;
    margin: 0px 15px 0px 42px;
    @media screen and (max-width: 1500px) {
      & {
        margin: 0px 15px 0px 15px;
      }
    }
    & .username {
      margin-left: 10px;
      font-family: 'SSans-SB';
      text-transform: uppercase;
      color: #404151;
    }
    & .user-avatar {
      border: 1px solid #cbcdd9;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      padding: 2px;
      width: 34px;
      height: 34px;
      box-sizing: border-box;
      & img {
        width: 100%;
        border-radius: 50%;
      }
    }
  }
  .myid {
    margin: 0px 10px;
    &-title {
      color: #7b7c90;
      font-family: 'SSans-Reg';
      & span {
        color: #404151;
      }
    }
  }
  .mygroup {
    margin: 0px 15px;
    &-title {
      color: #7b7c90;
      font-family: 'SSans-Reg';
      & span {
        color: #404151;
      }
    }
  }
  .category-changer {
    display: none;
  }
  .categories {
    margin: 0px 15px;
    &-title {
      color: #7b7c90;
      font-family: 'SSans-Reg';
      margin-right: 5px;
      & span {
        color: #404151;
      }
    }
    display: flex;
    align-items: center;
    & .items {
      display: flex;
      align-items: center;
      & .item {
        margin: 0px 3px;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #7b7c90;
        line-height: 10px;
        font-family: 'Ssans-SB';
        text-transform: uppercase;
        cursor: pointer;
        transition-duration: .2s;
        position: relative;
        z-index: 1;
        background: #f0f1f7;
        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(to bottom, #ff6f2f, #f64628);
          opacity: 0;
          z-index: 2;
          border-radius: 50%;
          transition-duration: .2s;
        }
        & span {
          position: relative;
          z-index: 3;
        }
        &:hover {
          &:before {
            opacity: 1;
          }
          & span {
            color: #fff;
          }
        }
        &.active {
          &:before {
            opacity: 1;
          }
          & span {
            color: #fff;
          }
        }
        &.disabled {
          cursor: default;
          &:hover {
            &:before {
              opacity: 0;
            }
            & span {
              color: #7b7c90;
            }
          }
          // &:before {
          //   opacity: 1;
          // }
        }
      }
    }
  }
  .tech-info {
    display: flex;
    align-items: center;
    position: relative;
    &.tech-active-notify {
      & .messages {
        margin-left: -152px;
      }
      & .notifications {
        & .tech-info-title {
          color: #404151;
        }
      }
    }
    &-title {
      margin: 0px 10px;
      color: #7b7c90;
      font-family: 'SSans-Reg';
    }
    & .icon {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      & img {
        width: 16px;
      }
    }
    & .round-amount {
        padding: 7px 9px;
        border-radius: 12px;
        line-height: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;
        font-family: 'SSans-Reg';
        text-transform: uppercase;
        cursor: pointer;
        background: linear-gradient(to bottom, #ff6f2f, #f64628);
      }
  }
  .notifications {
    display: flex;
    align-items: center;
    margin: 0px 11px;
    cursor: pointer;
    & img {
      margin-bottom: -2px;
    }
    &.active {
      height: 100%;
      background: #fff;
      position: relative;
      padding: 20px 20px;
      width: 320px;
      box-shadow: 0px 3px 5px 0px rgba(228, 229, 236, 1);
      z-index: 1001;
    }
    & .list {
      position: absolute;
      z-index: 1000;
      box-shadow: 0px 3px 5px 0px rgba(228, 229, 236, 1);
      top: 64px;
      left: 0px;
      width: 100%;
      box-sizing: border-box;
      background: #fff;
      border-bottom-left-radius: 12px;
      border-bottom-right-radius: 12px;
      & .item {
        padding: 18px 25px;
        font-family: 'SSans-Reg';
        line-height: 15px;
        width: 100%;
        box-sizing: border-box;
        border-top: 1px solid #dddfeb;
        color: #7b7c90;
        &.new {
          color: #404151;
          display: flex;
          align-items: center;
          & span {
            display: block;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background: linear-gradient(to bottom, #ff6f2f, #f64628);
            margin-right: 7px;
          }
        }
      }
      & .showMore {
        padding: 18px 25px;
        display: flex;
        align-items: center;
        border-top: 1px solid #dddfeb;
        color: #f44b2f;
        font-family: 'SSans-Reg';
        & img {
          margin-right: 7px;
          margin-top: -1px;
        }
      }
    }
  }
  .messages {
    display: flex;
    align-items: center;
    margin: 0px 11px;
    padding-right: 30px;
    cursor: pointer;
    & img {
      margin-bottom: -2px;
    }
  }
  .quit {
    width: 64px;
    height: 64px;
    border-left: 1px solid #dddfeb;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  @media screen and (max-width: 1150px) {
    .notifications.active {
      position: absolute;
      right: 0px;
      margin: 0px;
      & .list {
        top: 84px;
      }
    }
  }
  @media screen and (max-width: 1110px) {
    .account-main-info {
      margin: 0px 5px;
      & .username {
        margin-left: 5px;
      }
    }
    .myid {
      margin: 0px 5px;
    }
    .mygroup {
      margin: 0px 5px;
    }
    .categories {
      margin: 0px 10px;
    }
    .notifications {
      margin: 0px 6px;
    }
    .messages {
      margin: 0px 6px;
      padding-right: 10px;
    }
  }
  @media screen and (max-width: 990px) {
    .notifications {
      & .icon {
        margin-right: 5px;
      }
      & .tech-info-title {
        display: none;
      }
    }
    .messages {
      & .icon {
        margin-right: 5px;
      }
      & .tech-info-title {
        display: none;
      }
    }
  }
  @media screen and (max-width: 890px) {
    .logo {
      display: none;
    }
    .username {
      display: none;
    }
    .account-info {
      padding-left: 24px;
    }
    .account-main-info {
      margin: 0px;
    }
    .blur-background {
      position: absolute;
      top: 0px;
      left: 0px;
      right: 0px;
      bottom: 0px;
      background: rgba(59, 58, 84, 0.5);
      backdrop-filter: blur(5px); 
      -webkit-backdrop-filter: blur(5px); 
      z-index: 99;
    }
    .categories {
      position: relative;
      & .categories-title {
        display: none;
      }
      & .items {
        display: none;
      }
    }
    .category-changer {
      width: 60px;
      display: flex;
      align-items: center;
      background: #DDDFEB;
      border-radius: 15px 15px 15px 15px;
      user-select: none;
      cursor: pointer;
      position: absolute;
      z-index: 100;
      &.opened {
        background: linear-gradient(180deg, #FFFFFF 0%, #F0F1F7 100%);
        box-shadow: 0px 2px 3px rgba(64, 65, 81, 0.15);
        border-radius: 25px;
        width: 160px;
        padding: 10px;
        margin-left: -10px;
        & .category-arrow {
          transform: rotate(180deg);
        }
        & .extra {
          display: flex;
          align-items: center;
          margin: 0px 10px;
          & .category {
            margin-left: 10px;
            border-radius: 50%;
            height: 30px;
            width: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #7B7C8F;
            font-family: 'SSans-Reg';
            font-size: 1.5rem;
            text-transform: uppercase;
            &.category-allowed {
              border: 2px solid #DDDFEB;
            }
            &.category-notallowed {
              border: 2px dashed #DDDFEB;
              position: relative;
              &:after {
                content: '';
                position: absolute;
                bottom: -1px;
                right: -1px;
                width: 12px;
                height: 14px;
                background: url("../../../assets/header-icons/lock.svg");
              }
            }
            &:nth-child(1) {
              margin: 0px;
            }
          }
        }
      }
      & .current-category {
        border-radius: 50%;
        height: 30px;
        width: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: linear-gradient(180deg, #FF702F 0%, #F54428 100%);
        color: #fff;
        font-family: 'SSans-Reg';
        font-size: 1.5rem;
        text-transform: uppercase;
      }
      & .category-arrow {
        margin: 0px 12px 0px auto;
      }
    }
  }
</style>