<template>
  <div class="tickets">
      
    <div class="tickets-container">
      <div class="tickets-caption">
        <p class="tickets-caption-title">Билеты: <span>{{ successfullTicketsNumber }} / {{ tickets ? tickets.length : 0 }} завершено</span></p>
        <button class="btn-info"><span>i</span></button>
      </div>
      <div class="tickets-wrapper">
        <div class="tickets-wrapper-tickets">
          <hint></hint>
          <ticketsItems :tickets="tickets"></ticketsItems>
        </div>
        <exam class="exam"></exam>
      </div>
    </div>
  </div>
</template>

<script>

import hint from "@/components/desktop/tickets/hint"
import ticketsItems from "@/components/desktop/tickets/tickets"
import exam from "@/components/desktop/tickets/exam"
import axios from "axios"
export default {
  name: 'tickets',
  components: {
    hint, ticketsItems, exam
  },
  data: () => ({
    tickets: null
  }),
  created() {
    axios.get("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/tickets/", {
      headers: {
        'Authorization': 'Bearer ' + this.token.access,
        'Content-Type': 'application/JSON'
      },
      params: {
        category: this.$store.getters["common/allowed_categories"][0]
      }
    }).then(data => {
      console.log(data.data)
      this.tickets = data.data
    })
  },
  computed: {
    token () {
      return this.$store.getters['auth/token']
    },
    successfullTicketsNumber() {
      return this.tickets ? this.tickets.filter(i => i.is_successful).length : 0
    }
  }
}
</script>
 
<style lang="scss" scoped>
  .tickets-container {
    padding: 40px;
    box-sizing: border-box;
    & * {
      box-sizing: border-box;
    }
    @media screen and (max-width: 1500px) {
      & {
        padding: 24px 24px 24px 24px;
      }
    }
    @media screen and (max-height: 960px) {
      & {
        padding: 24px;
      }
    }
  }
  .tickets-caption {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    flex-wrap: wrap;
    &-title {
      margin: 0px 10px 0px 20px;
      font-family: 'Vox-Mit';
      color: #404151;
      font-size: 2.77rem;
      & span {
        color: #7b7c8f;
      }
    }

    & .btn-info {
      padding: 12px 16px;
      background: #fff;
      color: #7b7c90;
      font-family: 'SSans-Reg';
      font-size: 1rem;
      border: none;
      box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
      border-radius: 6px;
      user-select: none;
      margin-left: auto;
      width: 40px;
      height: 40px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      z-index: 98;
      &.active:after {
        content: ''; 
        position: absolute;
        left: 50%; 
        bottom: -14px;
        transform: translate(-50%, 0);
        border: 9px solid transparent;
        border-bottom: 9px solid #fff;
        z-index: 99;
        cursor: default;
      } 
      & span {
        border-radius: 50%;
        width: 16px;
        height: 16px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;
        font-family: 'SSans-SB';
        cursor: pointer;
        font-size: 1rem;
        background: linear-gradient(to bottom, #ff6f2f, #f64628);
      }
    }
  }
  .tickets-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 25px;
    &-tickets {
      width: 100%;
      max-width: calc(((100% / 12) * 9) - 12px);
      box-sizing: border-box;
    }
    & .exam {
      width: 100%;
      max-width: calc(((100% / 12) * 3) - 12px);
      box-sizing: border-box;
    }
  }
</style>
