<script>
export default {
  name: 'stats-table',
  data: function () {
    return  {
      cols: {
        1: null,
        2: null,
        3: null,
        4: null,
        5: null,
        6: null,
        7: null
      }
    }
  },
  mounted() {
    this.colsCalc()
    let vm = this
    window.onresize = function () {
      if(vm.$refs.statsTable) {
        vm.colsCalc()
      }
    }
  },
  methods: {
    colsCalc() {
      for(let i = 1; i <= 7; i++) {
        this.cols[i] = (+this.$refs.statsTable.clientWidth / 7) * i
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .stats-table {
    width: 100%;
    border-radius: 8px;
    box-sizing: border-box;
    box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
    & * {
      box-sizing: border-box;
    }
    &-head {
      width: 100%;
      box-sizing: border-box;
      & .row {
        width: 100%;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
