            & .text {
              margin: 0px;
              font-family: 'SSans-Reg';
              font-size: 1rem;
            }
            & .errors {
              margin: 0px;
              color: #7b7c8f;
              font-family: 'SSans-Reg';
              font-size: 1rem;
              margin-top: 5px;
            }
            & img {
              // width: 36px;
              // height: 24px;
              margin-bottom: 15px;
            }
          }
          &.true {
            & .text {
              color: #2fc916;
            }
            & .errors {
              border-bottom: 1px dashed #2fc916;
            }
          }
          &.false {
            & .text {
              color: #dc3457;
            }
            & .errors {
              border-bottom: 1px dashed #dc3457;
            }
          }
          &.default {
            & .errors {
              border: none !important;
            }
          }
        }
        & .rows {
          box-sizing: border-box;
          width: 100%;
          & .row {
            padding-right: 0px;
          }
        }
      }
      & .item:nth-child(even) {
        background: #f7f8fa;
      }
      & .item:nth-child(odd) {
        background: #ffffff;
      }
      & .item:last-child {
        border-bottom-right-radius: 8px;
        border-bottom-left-radius: 8px;
        & .row:last-child {
          border-bottom-left-radius: 8px;
        }
        & .result {
          border-bottom-right-radius: 8px;
        }
      }
      & .row {
        width: 100%;
        box-sizing: border-box;
        padding: 19px 40px;
        display: flex;
        align-items: center;
        // justify-content: space-between;
        user-select: none;
        border-bottom: 1px solid #dddfeb;
        & .text {
          &-numb {
            color: #404151;
            font-family: 'SSans-Reg';
            margin: 0px;
            font-size: 1rem;
          }
          padding-left: 24px;
          margin: 0px;
          font-family: 'SSans-Reg';
          color: #7b7c8f;
          font-size: 1rem;
          display: flex;
          align-items: center;
          &-item {
            margin: 0px;
          }
          & img {
            margin-right: 8px;
            // height: 12px;
            // width: 18px;
          }
          &.false {
            & img {
              margin-right: 16px;
            }
          }
          &.true {
            color: #2fc916;
            & .errors {
              color: #7b7c8f;
              margin: 0px;
              margin-left: 8px;
              margin-bottom: -1px;
              border-bottom: 1px dashed #2fc916;
            }
          }
          &.false {
            color: #dc3457;
            & .errors {
              color: #7b7c8f;
              margin: 0px;
              margin-left: 8px;
              margin-bottom: -1px;
              border-bottom: 1px dashed #dc3457;
              &.default {
                border: none !important;
                margin-bottom: 0px;
              }
            }
          }
        }
