<template>
  <div class="auth">
    <div class="auth-wrapper">
      <div class="auth-wrapper-logo">
        <img ondragstart="return false" src="../assets/auth/logo.png" alt="">
      </div>
      <div class="auth-wrapper-row">
        <div class="auth-wrapper-row-input input-1"><span>+7 <img ondragstart="return false" src="../assets/auth/arrow.png" alt=""></span></div>
        <input v-model="phone" placeholder="Номер телефона..." type="text" class="auth-wrapper-row-input input-2">
      </div>
      <div class="auth-wrapper-row">
        <input v-model="password" placeholder="Пароль..." type="text" class="auth-wrapper-row-input input-2">
      </div>
      <div class="auth-wrapper-row auth-wrapper-row-2">
        <label for="isForgetMe">
          <span class="checkbox-span"><span :class="isForgetMe ? 'true' : 'false'"></span></span>
          <input id="isForgetMe" v-model="isForgetMe" type="checkbox" class="checkbox">
          <p class="checkbox-text">Запомнить меня</p>
        </label>
        <p class="forgotPass">Я забыл пароль!</p>
      </div>
      <div class="auth-wrapper-row">
        <button :disabled="isActiveBtn" @click="login" class="login-btn">Войти</button>
      </div>
      <div class="auth-wrapper-row auth-wrapper-row-text">
        <p class="or">Или</p>
      </div>
      <div class="auth-wrapper-row">
        <button class="login-btn-vk"><img ondragstart="return false" src="../assets/auth/vk.png" alt="">Войти через Vkontakte</button>
      </div>
      <div class="auth-wrapper-row-reg">
        <p class="auth-wrapper-row-reg-title">Нет аккаунта?</p>
        <button @click="$router.push('registration')" class="login-btn-reg"><img ondragstart="return false" src="../assets/auth/reg.png" alt="">Зарегистрироваться</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"
export default {
  name: 'auth',
  data: () => ({
    isForgetMe: false,
    phone: '',
    password: '',
    isActiveBtn: false
  }),
  created() {
    
  },
  methods: {
    login() {
      if(this.phone && this.password) {
        this.isActiveBtn = true
        axios.post("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/token/create/", {
          "phone": "+7" + this.phone,
          "password": this.password
        }).then(data => {
          this.isActiveBtn = false
          localStorage.setItem('token', JSON.stringify(data.data))
          this.$store.dispatch('auth/SET_TOKEN', data.data)
          this.$router.push('/dashboard')
        }).catch(err => alert(err))
      }
    }
  }
}
</script>
 
