<script>
require('videojs-youtube');
import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'

export default {
  name: "LessonsDetails",
  data: () => ({
    playerOptions: {
      muted: true,
      language: 'ru',
      playbackRates: [0.5, 1.0, 1.25, 1.5, 2],
      techOrder: ['youtube'],
      sources: [{
        type: "video/youtube",
        src: null
      }],
      poster: null,
      fluid: true,
      youtube: {
        ytControls: 0,
        rel: 0,
        autohide: 1,
        showinfo: 0
      }
    },
    maxHeight: null,
    textareaHeight: 27,
    currentTime: null,
    isRendered: false,
    currentLesson: null,
    isViewed: false,
    time: [0],
    isChanged: false
  }),
  components: {
    videoPlayer
  },
  mounted() {
    this.playerOptions.sources[0].src = `https://www.youtube.com/watch?v=${this.$route.params.id}`
    this.playerOptions.poster = `https://img.youtube.com/vi/${this.$route.params.id}/hqdefault.jpg`
    this.isRendered = true
    this.currentLesson = this.$store.getters['common/lessons'].find(l => l.id === this.$route.query.id)
    if(!this.currentLesson) {
      this.$router.push('/dashboard')
    }
    setTimeout(() => {
      this.maxHeight = this.$refs.lessionVideo.clientHeight
      let vm = this
      setTimeout(() => {
        this.maxHeight = this.$refs.lessionVideo.clientHeight
        window.onresize = function () {
          if(vm.$refs.lessionVideo) {
            vm.maxHeight = vm.$refs.lessionVideo.clientHeight
          }
        }
      }, 1);
      setInterval(() => {
        if(this.$refs.lessionVideo) {
          this.maxHeight = this.$refs.lessionVideo.clientHeight
        }
      }, 100);      
    }, 1);
    setTimeout(() => {
      this.player.muted(false)
    }, 500)
  },
  destroyed() {
    
  },
  updated() {
    this.$refs.messagesFolder.scrollTop = this.$refs.messagesFolder.scrollHeight
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player
    },
  },
  methods: {
    textareaListener () {
      if(this.$refs.textarea.scrollTop > 0) {
        this.textareaHeight = this.$refs.textarea.scrollHeight 
      }
    },
    videoTimeHandler(player) {
      // player.preventDefault();
      // player.currentTime(10)
      // if(this.time >= this.player.currentTime() + 5) {
        // this.time1 = this.time2
        // this.player.currentTime(this.time1)
      // }
      if(player.currentTime() > this.time[this.time.length - 1] + 9) {
        player.currentTime(this.time[this.time.length - 1])
      }
    },
    onPlayerTimeupdate(player) {

      if(player.currentTime() > this.time[this.time.length - 1] + 2) {
        if(!this.isChanged) {
          this.isChanged = true
          player.currentTime(this.time[this.time.length - 1])
        }
      } else {
        this.time.push(player.currentTime())
        this.isChanged = false
      }
    }
  }
}
</script>

