<template>
<div class="navigation-folder">
  <div class="desktop">
    <div :class="isMenuOpened ? 'opened' : 'closed'" id="navigation">
      <div class="slots">
        <div v-if="!isDesktopFullHD" @click="toggleMenu" class="slot burger-menu">
          <div v-if="!isMenuOpened" class="icon">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
          <div v-else class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/back-arrow.png" alt="">
          </div>
        </div>
        <div v-else class="slot">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/logo.png" alt="">
          </div>
          <img ondragstart="return false" src="../../assets/nav-icons/logo-title.png" alt="">
        </div>
        <router-link tag="div" to="/dashboard" active-class="active-slot" class="slot slot-icon">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/1.png" alt="">
          </div>
          <p class="title">Рабочий стол</p>
        </router-link>
        <router-link tag="div" to="/shedule" active-class="active-slot" class="slot slot-icon">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/2.png" alt="">
          </div>
          <p class="title">Расписание</p>
        </router-link>
        <router-link tag="div" to="/tickets" active-class="active-slot" class="slot slot-icon">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/3.png" alt="">
          </div>
          <p class="title">Билеты</p>
        </router-link>
        <router-link tag="div" to="/statisitic" active-class="active-slot" class="slot slot-icon">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/4.png" alt="">
          </div>
          <p class="title">Статистика</p>
        </router-link>
        <router-link tag="div" to="/payment" active-class="active-slot" class="slot slot-icon">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/5.png" alt="">
          </div>
          <p class="title">Оплаты</p>
        </router-link>
        <router-link tag="div" to="/database" active-class="active-slot" class="slot slot-icon">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/6.png" alt="">
          </div>
          <p class="title">База знаний</p>
        </router-link>
        <router-link tag="div" to="/settings" active-class="active-slot" class="slot slot-icon">
          <div class="icon">
            <img ondragstart="return false" src="../../assets/nav-icons/7.png" alt="">
          </div>
          <p class="title">Настройки</p>
        </router-link>
      </div>
      <div class="apps">
        <div class="apps-slot">
          <img ondragstart="return false" src="../../assets/nav-icons/IOS.png" alt="">
        </div>
        <div class="apps-slot">
          <img ondragstart="return false" src="../../assets/nav-icons/AND.png" alt="">
        </div>
      </div>
    </div>
    <div :class="isMenuOpened ? 'active-bg' : 'disactive-bg'" class="darken-bg" @click.self="closeMenu"></div>
  </div>
  <div class="mobile">

  </div>
</div>
</template>

<script>
export default {
  name: 'navigation',
  data: () => ({
    isMenuOpened: false,
    isDesktopFullHD: false
  }),
  mounted() {
   window.addEventListener('resize', () => this.resizeMenuToggler())
   this.resizeMenuToggler()
  },
  created() {
    this.resizeMenuToggler()
  },
  methods: {
    toggleMenu () {
      this.isMenuOpened = !this.isMenuOpened
    },
    closeMenu () {
      this.isMenuOpened = false
    },
    resizeMenuToggler() {
      if(document.documentElement.clientWidth >= 1500) {
        this.isMenuOpened = true
        this.isDesktopFullHD = true
      } else {
        this.isMenuOpened = false
        this.isDesktopFullHD = false
      }
    }
  }
}
</script>


<style lang="scss">
.darken-bg{
  background: rgba(125, 126, 128, 0.4);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 98;
  visibility: hidden;
  transition-duration: .2s;
  opacity: 0;
  @media screen and (min-width: 1500px) {
    display: none;
  }
}
.active-bg {
  visibility: visible;
  opacity: 1;
}
#navigation {
  height: 100%;
  max-height: 100%;
  overflow-y: hidden;
  width: 64px;
  background: #404151;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  user-select: none;
  transition: width .2s;
  position: fixed;
  z-index: 99;
  &.opened {
    width: 240px;
    overflow: hidden;
    & .slot {
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      width: 240px;
      & .icon {
        width: 64px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
      &:hover {
        & .title {
          color: #404151;
        }
      }
      &.active-slot {
        & .title {
          color: #404151;
        }
      }
    }
  }
  &.closed {
    overflow: hidden;
    & .slot {
      align-items: flex-start;
      & .icon {
        width: 64px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
      & .title {
        display: none;
      }
    }
  }
}
.apps {
  display: flex;
  align-items: center;
  & .apps-slot {
    &:nth-child(1) {
      margin: 0px 20px 20px 20px;
    }
    margin-bottom: 20px;
    // width: 32px;
    // height: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  @media screen and (max-width: 1500px) {
    display: block;
    & .apps-slot {
      margin-top: 20px;
      align-items: flex-start;
      margin-left: 20px;
    }
  }
  @media screen and (max-height: 500px) {
    & .apps-slot {
      & img {
      zoom: .85;
      }
    }
  }
}
.slots {
  & .slot {
    width: 100%;
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    cursor: pointer;
    border-bottom: 1px solid #525362;
    box-sizing: border-box;
    transition: background .2s, border-color .2s; 
    @media screen and (max-height: 630px) {
      & {
        height: 48px;
      }
    }
    @media screen and (max-height: 500px) {
      & {
        height: 32px;
      }
      & img {
        zoom: .75;
      }
    }
    & img {
      display: block;
    }
    & .title {
      font-family: 'Vox-SBit';
      font-size: 1.066rem;
      color: #fff;
    }
    &.slot-icon:hover {
      background: #f0f1f7;
      border-color: #f0f1f7;
    }
    &.active-slot {
      background: #f0f1f7;
      border-color: #f0f1f7;
    }
  }
  & .burger-menu {
    & span {
      display: block;
      width: 24px;
      height: 1px;
      background: #fff;
      margin: 2px 0px 1px 0px;
      border-bottom: 1px solid #4c4d5c;
      &:last-child {
        border: none;
      }
    }
    @media screen and (max-height: 500px) {
      & span {
        width: 18px;
        margin: 1px 0px 1px 0px;
      }
    }
  }
}
@media screen and (min-width: 600px) {
  .navigation-folder .mobile {
    display: none;
  }
  .navigation-folder .desktop {
    display: block;
  }
}
@media screen and (max-width: 600px) {
  .navigation-folder .mobile {
    display: block;
  }
  .navigation-folder .desktop {
    display: none;
  }
}
</style>