<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import axios from "axios"
export default {
  name: 'band',
  components: {
    Swiper,
    SwiperSlide
  },
  data: () => ({
    swiperOption: {
      slidesPerView: 4,
      slidesPerGroup: 4,
      spaceBetween: 8,
      slidesPerColumn: 1,
      direction: 'horizontal',
      scrollbar: {
        el: '.swiper-scrollbar',
        dragClass: 'swiper-scrollbar-drag scroll-drag'
      },
      navigation: {
        nextEl: '.right-arrow',
        prevEl: '.left-arrow'
      },
      breakpoints: {
        1170: {
          slidesPerView: 4,
          slidesPerGroup: 4,
        },
        890: {
          slidesPerView: 3,
          slidesPerGroup: 3,
        },
        550: {
          slidesPerView: 2,
          slidesPerGroup: 2,
        }
      }
    },
    lessons: null
  }),
  methods: {
    goToTrain(item) {
      this.$router.push(`dashboard/training/${item.id}`)
    },
    goToExam(item) {
      this.$router.push(`dashboard/exam/${item.id}`)
    },
    routeToLesson (lesson) {
      this.$router.push({
        path: `dashboard/lessons/${lesson.youtube_code}`, 
        query: {id: lesson.id}
      })
    }
  },
  computed: {
    token() {
      return this.$store.getters["auth/token"]
    },
    lessons_getters() {
      return this.$store.getters['common/lessons']
    }
  },
  created() {
    console.log(this.lessons_getters)
  },
  mounted() {
    this.lessons = this.lessons_getters
    console.log(this.lessons)
    // axios.get("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/lessons/", {
