    //   headers: {
    //     'Authorization': 'Bearer ' + this.token.access,
    //     'Content-Type': 'application/JSON'
    //   }
    // }).then(data => {
    //   this.lessons = data.data
    //   this.$store.dispatch('common/SET_LESSONS', data.data)
    // })
  },
  watch: {
    lessons_getters: function () {
      this.lessons = this.lessons_getters
    }
  },
}
</script>

<style lang="scss" scoped>

