<script>
export default {
  name: "lessions-mobile",
  data: () => ({
    containerHeight: null
  }),
  mounted() {
    let doc = document.documentElement
    if(doc.clientWidth >= 600) {
      this.$router.push("/dashboard")
    }
    window.addEventListener('resize', () => this.resizeEvent())
    let docHeight = doc.clientHeight
    let captionHeight = this.$refs.caption.clientHeight
    this.containerHeight = docHeight - captionHeight - 80
  },
  methods: {
    resizeEvent () {
      let doc = document.documentElement
      if(doc.clientWidth >= 600 && this.$route.path === '/lessions') {
        this.$router.push("/dashboard")
      }
    }
  }
}
</script>

