<template>
  <div class="timer">
    <div class="timer-main">
      <p class="timer-main-title">{{ hours }}:{{ minutes }}:{{ seconds }}</p>
      <p class="timer-main-desc">Времени осталось</p>
    </div>
    <div class="timer-footer">
      <p class="timer-footer-text">Отвечено: {{ answeredQ }} из 7</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "timer",
  props: ["timeAmount", "answeredQ"],
  data: () => ({
    hours: '00',
    minutes: '00',
    seconds: '00'
  }),
  created() {
    let time = this.timeAmount
    setInterval(() => {
      if(time >= 1) {
        let m = Math.floor(time / 60)
        let s = time % 60
        if(m.toString().length === 1) {
          this.minutes = `0${m}`
        } else {
          this.minutes = m
        }
        if(s.toString().length === 1) {
          this.seconds = `0${s}`
        } else {
          this.seconds = s
        }
        time--
      } else {
        this.minutes = '00'
        this.seconds = '00'
      }
    }, 1000);
  },
  methods: {

  }
}
</script>

<style lang="scss" scoped>
  .timer {
    position: absolute;
    top: 50%;
    right: 0px;
    transform: translate(0, -50%);
    width: 200px;
    height: 170px;
    user-select: none;
    &-main {
      height: 122px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-top-left-radius: 8px;
      background: linear-gradient(to bottom, #7b7c8f 93%, #696a7c 100%);
      &-title {
        margin: 0px;
        font-family: 'Vox-Mit';
        font-size: 2.15rem;
        color: #fff;
      }
      &-desc {
        font-family: 'SSans-Reg';
        font-size: 1rem;
        color: #fff;
        margin: 0px;
      }
    }
    &-footer {
      background: #7b7c8f;
      border-bottom-left-radius: 8px;
      &-text {
        margin: 0px;
        font-family: 'Vox-Mit';
        font-size: 1.33rem;
        color: #fff;
        text-align: center;
        padding: 17px;
      }
    }
  }
</style>