<template>
  <div class="mobile-navigation">
    <div :class="isOpenNav ? '' : 'closed'" :style="`z-index: ${zIndexNav}`" class="nav-wrapper">
      <div class="items">
        <div v-for="item in navigation" :key="item.title" @click="routeTo(item)" :class="isActiveItem(item)" class="item" >
          <div class="icon">
            <img ondragstart="return false" :src="require(`../../assets/nav-icons/mobile-icons/icon-${item.img}${isActiveItem(item) === 'active' ? '-active' : ''}.svg`)" alt="">
          </div>
          <p class="item-title">{{ item.title }}</p>
        </div>
      </div>
      <div @click="toggleNavigation" class="btn-toggler">
        <span class="btn-toggler-item-1"></span>
        <span class="btn-toggler-item-2"></span>
      </div>
    </div>
    <div @click.self="isOpenNav = false" :class="isOpenNav ? 'blur-background-active' : ''" class="blur-background"></div>
  </div>
</template>

<script>
export default {
  name: "mobile-navigation",
  data: () => ({
    isOpenNav: false,
    navigation: [
      {
        title: 'Главная',
        url: '/dashboard',
        img: '1'
      },
      {
        title: 'Уроки',
        url: '/lessions',
        img: '2'
      },
      {
        title: 'Расписание',
        url: '/23121323123',
        img: '3'
      },
      {
        title: 'Теория',
        url: '/23121323123',
        img: '4'
      },
      {
        title: 'Билеты',
        url: '/23121323123',
        img: '5'
      },
      {
        title: 'Уведомления',
        url: '/23121323123',
        img: '6'
      },
      {
        title: 'Оплаты',
        url: '/23121323123',
        img: '7'
      },
      {
        title: 'Настройки',
        url: '/23121323123',
        img: '8'
      },
      {
        title: 'Статистика',
        url: '/23121323123',
        img: '9'
      },
      {
        title: 'Сообщения',
        url: '/23121323123',
        img: '10'
      },
    ]
  }),
  methods: {
    toggleNavigation () {
      this.isOpenNav = !this.isOpenNav
    },
    routeTo (item) {
      if(item.url !== this.$route.path) {
        this.$router.push(item.url)
      }
    },
    isActiveItem (item) {
      if(item.url === this.$route.path) {
        return 'active'
      }
    }
  },
  computed: {
    zIndexNav() {
      return this.isOpenNav ? '10001' : '9997'
    }
  }
}
</script>

<style lang="scss" scoped>
  .blur-background {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background: rgba(59, 58, 84, 0.5);
    backdrop-filter: blur(5px); 
    -webkit-backdrop-filter: blur(5px); 
    z-index: 10000;
    visibility: hidden;
    opacity: 0;
    transition-duration: .3s;
  }
  .blur-background-active {
    visibility: visible;
    opacity: 1;
  }
  @media screen and (min-width: 600px) {
    .mobile-navigation {
      display: none;
    }
  }
  .nav-wrapper {
    padding: 22px 10px 70px 10px;
    background: #404151;
    width: 100%;
    max-height: 260px;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 9997;
    box-sizing: border-box;
    transition-duration: .2s;
    overflow: hidden;
    &.closed {
      max-height: 80px;
      padding: 0px 10px;
      & .item {
        &:nth-child(3) {
          opacity: 0;
        }
      }
      & .btn-toggler {
        background: linear-gradient(180deg, #404151 0%, #30313D 100%);
        box-shadow: 0px 15px 20px rgba(40, 41, 53, 0.6), inset 0px 1px 0px #5C5D6F;
        bottom: 10px;
        left: 50%;
        transform: translate(-50%, 0);
        cursor: pointer;
        &-item-2 {
          opacity: 1;
          transform: rotate(0deg);
          visibility: visible;
        }
      }
    }
    & .items {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    & .btn-toggler {
      margin: 0px auto;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background: linear-gradient(180deg, #30313D 0%, #404151 100%);
      box-shadow: 0px 15px 20px rgba(40, 41, 53, 0.6), inset 0px 1px 0px #5C5D6F;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 10px;
      left: 50%;
      transform: translate(-50%, 0);
      user-select: none;
      &-item-1 {
        width: 16px;
        height: 2px;
        display: block;
        background: #fff;
        position: absolute;
        transition-duration: .2s;
      }
      &-item-2 {
        width: 2px;
        height: 16px;
        display: block;
        opacity: 0;
        visibility: hidden;
        background: #fff;
        position: absolute;
        transition-duration: .2s;
        transform: rotate(90deg);
      }
    }
    & .item {
      width: calc(100% / 5);
      height: 71px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 6px 0px;
      transition-duration: .2s;
      user-select: none;
      cursor: pointer;
      &.active .item-title {
        color: #fff;

      }
      & .icon {
        display: flex;
        align-items: center;
        justify-content: space-between;
        // width: 24px;
        // height: 22px;
        & img {
          width: 24px;
          height: 22px;
        }
      }
      &.active .icon img {
        width: 32px;
        height: 30px;
        margin-bottom: -4px;
        // margin-top: 6px;
      }
      &-title {
        color: #9596A9;
        margin: 11px 0px 0px 0px;
        font-family: 'SSans-Reg';
        font-size: 1.2rem;
        line-height: 1.5rem;
        text-align: center;
      }
      &.active {
        & .item-title {
          // margin-top: 0px;
        }
      }
    }
  }
</style>