<template>
  <div class="reg">
    <div class="reg-wrapper">
      <div class="reg-wrapper-logo">
        <img ondragstart="return false" src="../assets/auth/logo-icon.png" alt="">
        <p class="reg-wrapper-logo-title">Регистрация</p>
      </div>
      <div class="reg-wrapper-row">
        <div class="reg-wrapper-row-input input-1"><span>+7 <img ondragstart="return false" src="../assets/auth/arrow.png" alt=""></span></div>
        <input v-model="userReg.phone" placeholder="Номер телефона..." type="text" class="reg-wrapper-row-input input-2">
      </div>
      <div class="reg-wrapper-row">
        <input v-model="userReg.name" placeholder="Ваше имя..." type="text" class="reg-wrapper-row-input input-2">
      </div>
      <div class="reg-wrapper-row">
        <button @click="access" :disabled="isDisabled" class="login-btn">Далее</button>
      </div>
      <div class="reg-wrapper-row cancel">
        <img @click="$router.push(`/auth`)" ondragstart="return false" src="../assets/auth/back.png" alt="">
        <p @click="$router.push(`/auth`)" class="cancel-text">Отменить и вернуться ко входу</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import https from 'https';
export default {
  name: 'reg',
  data: () => ({
    userReg: {
      phone: '',
      name: ''
    },
    isActiveBtn: false
  }),
  computed: {
    isDisabled() {
      if(this.userReg.phone && this.userReg.name && !this.isActiveBtn) {
        return false
      } else {
        return true
      }
    }
  },
  methods: {
    access () {
      // this.$router.push('/registration/phone-vetification')
      this.isActiveBtn = true
      axios.post("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/students/", {
          "first_name": this.userReg.name,
          "phone": "+7" + this.userReg.phone
      }).then(data => {
        this.$store.dispatch('registration/SET_USERDATA', {'key': 'phone', 'value': '+7' + this.userReg.phone})
        this.$store.dispatch('registration/SET_USERDATA', {'key': 'name', 'value': this.userReg.name})
        this.$router.push('/registration/phone-verification')
        this.isActiveBtn = false
      }).catch(err => {
        if(localStorage.getItem('token')) {
          this.isActiveBtn = false
          alert('Возникла ошибка')
        }
      })
    }
  }
}
</script>
 
<style lang="scss" scoped>
  .reg {
    &-wrapper {
      min-height: 100vh;
      height: 100%;
      margin: 0px auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      max-width: 320px;
      &-logo {
        margin: 20px auto;
        margin-top: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        user-select: none;
        & img {
          margin: 0px auto;
        }
        &-title {
          font-family: 'Vox-Mit';
          font-size: 1.6rem;
          color: #fff;
          margin: 10px 0px 0px 0px;
        }
      }
      &-row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 12px -4px;
        user-select: none;
        &-input {
          background: transparent;
          border: 1px solid #7b7c8f;
          height: 48px;
          display: flex;
          align-items: center;
          padding: 15px 16px;
          margin: 0px 4px;
          border-radius: 24px;
          user-select: none;
          &.input-1 {
            cursor: pointer;
            & span {
              color: #dddfeb;
              font-family: 'SSans-Reg';
              font-size: 1.2rem;
              display: flex;
              align-items: center;
            }
            & img {
              margin-left: 24px;
              margin-top: 3px;
            }
          }
          &.input-2 {
            color: #dddfeb;
            font-family: 'SSans-Reg';
            font-size: 1.2rem;
            width: 100%;
            &::placeholder {
              color: #dddfeb;
              font-family: 'SSans-Reg';
              font-size: 1.2rem;
            }
          }
        }
        &-text {
          margin: 0px 0px;
        }
        & .login-btn {
          width: 100%;
          color: #fff;
          font-size: 1.2rem;
          font-family: 'SSans-Reg';
          height: 48px;
          background: linear-gradient(to bottom, rgb(255, 111, 47), #f64628);
          border-radius: 24px;
          border: none;
          cursor: pointer;
          outline: none;
          &:disabled {
            background: rgba(127, 127, 127, .8);
            color: rgba(255, 255, 255, .8);
          }
        }
        &.cancel {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-top: auto;
          & img {
            margin-right: 10px;
            cursor: pointer;
          }
          & .cancel-text {
            color: #f44b2f;
            font-family: 'SSans-Reg';
            font-size: 1rem;
            cursor: pointer;
          }
        }
      }
    }
  }
</style>
