export default {
  namespaced: true,
  state: {
    modals: []
  },
  getters: {
    modals(state) {
      return state.modals
    }
  },
  mutations: {
    addModal(state, modal) {
      state.modals.push(modal)
    }
  },
  actions: {
    ADD_MODAL({commit}, modal) {
      commit('addModal', modal)
    }
  }
}