<template>
  <div class="mobile-header">
    <div class="first-bg">
      <div class="header-first-part">
        <div class="first-part">
          <div @click="isCategoryChangerOpen = !isCategoryChangerOpen" class="category-changer" :class="isCategoryChangerOpen ? 'opened' : ''">
            <span class="current-category">{{ currentCategory }}</span>
            <div class="extra" v-show="isCategoryChangerOpen">
              <span @click="c.isAllowed ? categoryChanger(c.title) : false" :class="c.isAllowed ? 'category-allowed' : 'category-notallowed'" v-for="c in filteredCategories" :key="c.title" class='category'>
                {{ c.title }}
              </span>
            </div>
            <img ondragstart="return false" class="category-arrow" src="../../../assets/header-icons/right-arrow.svg" alt="">
          </div>
          <div class="greetings">
            <p class="greetings-subtitle">Приветствуем,</p>
            <p class="greetings-title">Анастасия!</p>
          </div>
        </div>
        <div class="extra-info">
          <div class="item group">
            <p class="desc">Группа: <span>6</span></p>
          </div>
          <div class="item id">
            <p class="desc">ID: <span>1473</span></p>
          </div>
        </div>
      </div>
      <div class="header-second-part">
        <div class="header-second-part-l">
          <p class="title">45%</p>
          <p class="desc">Пройдено<br/>вождений</p>
        </div>
        <div class="header-second-part-avatar">
          <svg
            class="svg-progress-1"
            viewBox="0 0 100 100"
          ><g
            fill-opacity="0"
            stroke-width="3"
          ><path
            d="M5 50a45 45 0 1 1 90 0"
            stroke="#DDDFEB"
          /><path
            class="progress-1"
            ref="progress1"
            d="M5 50a45 45 0 1 1 90 0"
            stroke="#F44B2F"
            stroke-dasharray="142"
            stroke-dashoffset="142"
          /></g></svg>
          <img ondragstart="return false" src="../../../assets/header-icons/avatar.png" alt="">
          <svg
            class="svg-progress-2"
            viewBox="0 0 100 100"
          ><g
            fill-opacity="0"
            stroke-width="3"
          ><path
            d="M5 50a45 45 0 1 1 90 0"
            stroke="#DDDFEB"
          /><path
            class="progress-2"
            ref="progress2"
            d="M5 50a45 45 0 1 1 90 0"
            stroke="#404151"
            stroke-dasharray="142"
            stroke-dashoffset="142"
          /></g></svg>
        </div>
        <div class="header-second-part-r">
          <p class="title">27%</p>
          <p class="desc">Освоено<br/>теории</p>
        </div>
      </div>
    </div>
    <div class="header-third-part">
      <div class="options">
        <div class="options-item">
          <img ondragstart="return false" class="options-item-icon" src="../../../assets/header-icons/notifications-icon.svg" alt="">
          <p class="desc">Уведомления:</p>
          <p class="status">Новых нет</p>
        </div>
        <div class="options-item">
          <img ondragstart="return false" class="options-item-icon" src="../../../assets/header-icons/settings-icon.svg" alt="">
          <p class="desc">Мой профиль</p>
        </div>
        <div class="options-item new">
          <img ondragstart="return false" class="options-item-icon" src="../../../assets/header-icons/messages-icon-active.svg" alt="">
          <p class="desc">Сообщения:</p>
          <p class="status">2 новых</p>
        </div>
      </div>
    </div>
    <div @click="isCategoryChangerOpen = false" v-show="isCategoryChangerOpen" class="blur-background"></div>
  </div>
</template>

<script>
export default {
  name: 'mobile-header',
  data: () => ({
    isCategoryChangerOpen: false,
    currentCategory: 'A',
    categories: [
      {
        title: 'A',
        isAllowed: true
      },
      {
        title: 'B',
        isAllowed: true
      },
      {
        title: 'C',
        isAllowed: false
      }
    ]
  }),
  methods: {
    categoryChanger (e) {
      this.currentCategory = e
      setTimeout(() => {
        this.isCategoryChangerOpen = false
      }, 0);
    },
    categoryChangerHideOrShow() {
      setTimeout(() => {
        this.isCategoryChangerOpen = !this.isCategoryChangerOpen
      }, 0);
    }
  },
  computed: {
    filteredCategories () {
      let categories = []
      for(let i = 0; i < this.categories.length; i++) {
        if(this.categories[i].title !== this.currentCategory) {
          categories.push(this.categories[i])
        }
      }
      return categories
    }
  },
  mounted() {
    let graph1 = this.$refs.progress1
    let graph2 = this.$refs.progress2
    let data = {
      val1: 45,
      val2: 27
    }
    let graph1Value = (142 - (data.val1 * 142 / 100))
    let graph2Value = (142 - (data.val2 * 142 / 100))
    graph1.style.strokeDashoffset = graph1Value
    graph2.style.strokeDashoffset = graph2Value
  }
}
</script>

<style lang="scss" scoped>
.progress-1 {
  transition-duration: 2s;
  stroke-dashoffset: 142;
}
.svg-progress-1 {
  transform: rotate(270deg);
  width: 142px;
  height: 142px;
  position: absolute;
  left: -15px;
}

.progress-2 {
  transition-duration: 2s;
  stroke-dashoffset: 142;
}
.svg-progress-2 {
  transform: rotate(90deg) scale(-1, 1);
  width: 142px;
  height: 142px;
  position: absolute;
  right: -15px;
}


  .blur-background {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background: rgba(59, 58, 84, 0.5);
    backdrop-filter: blur(5px); 
    -webkit-backdrop-filter: blur(5px); 
    z-index: 9998;
  }
  .first-bg {
    background: linear-gradient(180deg, #FFFFFF 42.71%, #F0F1F7 100%);
    box-shadow: 0px 1px 5px rgba(64, 65, 81, 0.15);
  }
  .header-first-part {
    position: relative;
    width: 100%;
    background: linear-gradient(180deg, #FFFFFF 55.73%, #F0F1F7 70.31%);
    box-shadow: 0px 1px 5px rgba(64, 65, 81, 0.2);
    padding: 10px 30px;
    box-sizing: border-box;
    & .first-part {
      display: flex;
      align-items: flex-start;
      position: relative;
    }
    & .category-changer {
      width: 60px;
      display: flex;
      align-items: center;
      background: #DDDFEB;
      border-radius: 15px 15px 15px 15px;
      user-select: none;
      cursor: pointer;
      position: absolute;
      z-index: 9999;
      &.opened {
        background: linear-gradient(180deg, #FFFFFF 0%, #F0F1F7 100%);
        box-shadow: 0px 2px 3px rgba(64, 65, 81, 0.15);
        border-radius: 25px;
        width: 160px;
        padding: 10px;
        & .category-arrow {
          transform: rotate(180deg);
        }
        & .extra {
          display: flex;
          align-items: center;
          margin: 0px 10px;
          & .category {
            margin-left: 10px;
            border-radius: 50%;
            height: 30px;
            width: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #7B7C8F;
            font-family: 'SSans-Reg';
            font-size: 1.5rem;
            &.category-allowed {
              border: 2px solid #DDDFEB;
            }
            &.category-notallowed {
              border: 2px dashed #DDDFEB;
              position: relative;
              &:after {
                content: '';
                position: absolute;
                bottom: -1px;
                right: -1px;
                width: 12px;
                height: 14px;
                background: url("../../../assets/header-icons/lock.svg");
              }
            }
            &:nth-child(1) {
              margin: 0px;
            }
          }
        }
      }
      & .current-category {
        border-radius: 50%;
        height: 30px;
        width: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: linear-gradient(180deg, #FF702F 0%, #F54428 100%);
        color: #fff;
        font-family: 'SSans-Reg';
        font-size: 1.5rem;
      }
      & .category-arrow {
        margin: 0px 12px 0px auto;
      }
    }
    & .greetings {
      text-align: center;
      font-size: 2rem;
      color: #404151;
      font-family: 'Exo 2', sans-serif;
      margin: 0px auto;
      &-subtitle {
        margin: 0px;
        font-weight: 500;
      }
      &-title {
        margin: 0px;
        font-weight: 600;
      }
    }
    & .extra-info {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: -15px;
      & .item {
        width: 60px;
        text-align: center;
        font-family: 'SSans-Reg';
        color: #7B7C8F;
        font-size: 1.2rem;
        line-height: 1.2rem;
        & span {
          font-family: 'SSans-SB';
          color: #404151;
        }
      }
    }
  }
  .header-second-part {
    display: flex;
    align-items: center;
    padding: 0px 10px;
    justify-content: space-between;
    height: 85px;
    box-sizing: border-box;
    position: relative;
    & .header-second-part-l {
      width: 100px;
      text-align: center;
      & .title {
        font-family: 'Exo 2', sans-serif;
        font-weight: 500;
        font-size: 3rem;
        line-height: 3.6rem;
        color: #F44B2F;
        margin: 0px 0px 5px 0px;
      }
      & .desc {
        font-family: 'SSans-Reg';
        font-size: 1.2rem;
        line-height: 1.5rem;
        margin: 0px;
        color: #404151;
      }
    }
    // ПОМЕНЯТЬ
    & .header-second-part-avatar {
      border-radius: 50%;
      border: 2px solid #7B7C8F;
      padding: 3px;
      background: #F0F1F7;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 110px;
      height: 110px;
      position: relative;
    }
    // 
    & .header-second-part-r {
      width: 100px;
      text-align: center;
      & .title {
        font-family: 'Exo 2', sans-serif;
        font-weight: 500;
        font-size: 3rem;
        line-height: 3.6rem;
        color: #404151;
        margin: 0px 0px 5px 0px;
      }
      & .desc {
        font-family: 'SSans-Reg';
        font-size: 1.2rem;
        line-height: 1.5rem;
        margin: 0px;
        color: #404151;
        white-space: wrap;
      }
    }
  }
  .header-third-part {
    padding: 22px 10px 17px 10px;
    & .options {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      &-item {
        width: 100px;
        text-align: center;
        position: relative;
        user-select: none;
        &-icon {
          margin-bottom: 8px;
          position: relative;
        }
        & .desc {
          color: #404151;
          font-family: 'SSans-Reg';
          font-size: 1.2rem;
          line-height: 1.5rem;
          margin: 0px;
          text-align: center;
        }
        & .status {
          color: #7B7C8F;
          font-family: 'SSans-Reg';
          font-size: 1.2rem;
          line-height: 1.5rem;
          margin: 0px;
          text-align: center;
        }
        &.new .status {
          color: #F44B2F;
        }
        &.new:after {
          content: '';
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background: linear-gradient(180deg, #FF702F 0%, #F54428 100%);
          position: absolute;
          top: -7px;
          right: calc(50% - 24px);
        }
      }
    }
  }
</style>