<template>
  <div class="reg">
    <div class="reg-wrapper">
      <div class="reg-wrapper-logo">
        <img ondragstart="return false" src="../assets/auth/logo-icon.png" alt="">
        <p class="reg-wrapper-logo-title">Подтверждение номера</p>
      </div>
      <p class="reg-wrapper-text">
        На ваш номер отправлен SMS с кодом верификации. Введите его в поле ниже:
      </p>
      <div class="reg-wrapper-row">
        <input maxlength="6" v-model="code" placeholder="Код верификации..." type="text" class="reg-wrapper-row-input input-2">
      </div>
      <div class="reg-wrapper-row">
        <button @click="access" :disabled="isDisabled" class="login-btn">Далее</button>
      </div>
      <div class="reg-wrapper-resend">
        <p class="reg-wrapper-resend-title"><span @click="resendSMS">Отправить SMS</span> повторно чере:</p>
        <p class="reg-wrapper-resend-timer">00:{{ timer > 9 ? timer : '0' + timer }}</p>
      </div>
      <div class="reg-wrapper-row cancel">
        <img @click="$router.push(`/auth`)" ondragstart="return false" src="../assets/auth/back.png" alt="">
        <p @click="$router.push(`/auth`)" class="cancel-text">Отменить и вернуться ко входу</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"
export default {
  name: 'registration_verification',
  data: () => ({
    code: '',
    timer: 59,
    isActiveBtn: false
  }),
  computed: {
    isDisabled() {
      if(this.code.length >= 4 && !this.isActiveBtn) {
        return false
      } else {
        return true
      }
    },
    userData() {
      return this.$store.getters['registration/userData']
    }
  },
  created() {
    setInterval(() => {
      if(this.timer > 0) {
        this.timer--
      }
    }, 1000);
  },
  methods: {
    access () {
      // this.$router.push('/registration/password')
      this.isActiveBtn = true
      axios.post("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/students/phone_confirm/", {
        'phone': this.userData.phone,
        'code': this.code
      }).then(data => {
        this.$router.push('/registration/password')
        // this.$store.dispatch('auth/SET_TOKEN', data.data)
        localStorage.setItem("token", JSON.stringify(data.data))
        this.isActiveBtn = false
      }).catch(err => {
        if(localStorage.getItem('token')) {
          alert('Этот пользователь уже существует. Пожалуйста войдите или, если вы забыли пароль, восстановите его')
        }
        this.isActiveBtn = false
      })
    },
    resendSMS () {
      if(this.timer === 0) {
        axios.post("https://cab.dev1.xn--80aafygkndd3b.xn--p1ai/api/students/", {
            "first_name": this.userData.name,
            "phone": this.userData.phone
        }).then(data => {
          this.timer = 59
        }).catch(err => alert(err))
      }
    }
  }
}
</script>
 
<style lang="scss" scoped>
  .reg {
    &-wrapper {
      min-height: 100vh;
      height: 100%;
      margin: 0px auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      user-select: none;
      max-width: 320px;
      &-text {
        color: #dddfeb;
        font-family: 'SSans-Reg';
        font-size: 1rem;
        margin: 10px 0px;
        line-height: 1.2rem;
        max-width: 320px;
        text-align: center;
      }
      &-resend {
        display: flex;
        flex-direction: column;
        text-align: center;
        margin-top: 30px;
        &-title {
          text-align: center;
          color: #dddfeb;
          font-family: 'SSans-Reg';
          margin: 0px;
          font-size: 1rem;
          & span {
            color: #f44b2f;
            cursor: pointer;
          }
        }
        &-timer {
          font-size: 2.175rem;
          font-family: 'Vox-Mit';
          margin: 0px;
          margin-top: 5px;
          color: #fff;
        }
      }
      &-logo {
        margin: 20px auto;
        margin-top: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        user-select: none;
        & img {
          margin: 0px auto;
        }
        &-title {
          font-family: 'Vox-Mit';
          font-size: 1.6rem;
          color: #fff;
          margin: 10px 0px 0px 0px;
        }
      }
      &-row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 12px -4px;
        user-select: none;
        &-input {
          background: transparent;
          border: 1px solid #7b7c8f;
          height: 48px;
          display: flex;
          align-items: center;
          padding: 15px 16px;
          margin: 0px 4px;
          border-radius: 24px;
          user-select: none;
          text-align: center;
          &.input-1 {
            cursor: pointer;
            & span {
              color: #dddfeb;
              font-family: 'SSans-Reg';
              font-size: 1.2rem;
              display: flex;
              align-items: center;
            }
            & img {
              margin-left: 24px;
              margin-top: 3px;
            }
          }
          &.input-2 {
            color: #dddfeb;
            font-family: 'SSans-Reg';
            font-size: 1.2rem;
            width: 100%;
            &::placeholder {
              color: #dddfeb;
              font-family: 'SSans-Reg';
              font-size: 1.2rem;
            }
          }
        }
        &-text {
          margin: 0px 0px;
        }
        & .login-btn {
          width: 100%;
          color: #fff;
          font-size: 1.2rem;
          font-family: 'SSans-Reg';
          height: 48px;
          background: linear-gradient(to bottom, rgb(255, 111, 47), #f64628);
          border-radius: 24px;
          border: none;
          cursor: pointer;
          outline: none;
          &:disabled {
            background: rgba(127, 127, 127, .8);
            color: rgba(255, 255, 255, .8);
          }
        }
        &.cancel {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-top: auto;
          & img {
            margin-right: 10px;
            cursor: pointer;
          }
          & .cancel-text {
            color: #f44b2f;
            font-family: 'SSans-Reg';
            font-size: 1rem;
            cursor: pointer;
          }
        }
      }
    }
  }
</style>
