<template>
  <div class="choosing">
      
    <div class="choosing-container">
      <div class="choosing-caption">
        <p class="choosing-caption-title"><span>Шаг 2: </span>Укажите параметры поиска автошколы</p>
        
        <div class="choosing-btns">
          <div class="toggler">
            <div @click="toggleMenu(i)" :class="i.isActive ? 'active' : ''" v-for="i in togglerMenu" :key="i.title" class="toggler-item">{{ i.title }}</div>
          </div>
          <button @click="$router.push('/payment')" class="back"><img ondragstart="return false" src="../assets/payment/close.png" alt="">Отменить</button>
        </div>
      </div>
      <div class="wrapper">
        <div v-if="currentComponent === 'list'" class="swiper-slides">
          <swiper class="swiper" :options="swiperOption">
            <swiper-slide v-for="i of 27" :key="i" class="card">
              <div class="card-main">
                <p class="card-main-title">Старт Учебный Центр</p>
                <div class="card-main-adress">
                  <img ondragstart="return false" src="../assets/payment/spot.png" alt="">
                  <p class="card-main-adress-text">Пр. Героев Сталинграда, 123 А</p>
                </div>
                <p class="card-main-price">От 22 500 <span>Р</span></p>
              </div>
              <div class="card-btn">
                <button @click="$router.push(`/payment/school/rates`)" class="card-btn-item">Подробнее</button>
              </div>
            </swiper-slide>
            <div class="swiper-scrollbar" slot="scrollbar"></div>
          </swiper>
          <div class="left-arrow" slot="button-prev">
            <img ondragstart="return false" src="../assets/dashboard-icons/right-arrow-slider-btn.svg" alt="">
          </div>
          <div class="right-arrow" slot="button-next">
            <img ondragstart="return false" src="../assets/dashboard-icons/right-arrow-slider-btn.svg" alt="">
          </div>
        </div>
        <div class="map" v-else>
          <div class="map-item" style="position:relative;overflow:hidden;"><a href="https://yandex.ru/maps/2/saint-petersburg/?utm_medium=mapframe&utm_source=maps" style="color:#eee;font-size:12px;position:absolute;top:0px;"></a><a href="https://yandex.ru/maps/2/saint-petersburg/?ll=30.335778%2C59.921792&utm_medium=mapframe&utm_source=maps&z=12.16" style="color:#eee;font-size:12px;position:absolute;top:14px;"></a><iframe src="https://yandex.ru/map-widget/v1/-/CCQrvHf28A" allowfullscreen="true" style="position:relative;"></iframe></div>
        </div>
        <div class="btns">
          <button @click="back" class="back-btn">
            <img ondragstart="return false" src="../assets/payment/back.png" alt="">
            Назад к параметрам
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
 
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
export default {
  name: 'choosing',
  data: () => ({
    swiperOption: {
      direction: 'horizontal',
      slidesPerView: 4,
      slidesPerColumn: 2,
      slidesPerColumnFill: 'row',
      spaceBetween: 8,
      scrollbar: {
        el: '.swiper-scrollbar',
        dragClass: 'swiper-scrollbar-drag scroll-drag'
      },
      navigation: {
        nextEl: '.right-arrow',
        prevEl: '.left-arrow'
      },
    },
    togglerMenu: [
      {
        title: 'На карте',
        route: 'map',
        isActive: false
      },
      {
        title: 'Списком',
        route: 'list',
        isActive: true
      }
    ],
  }),
  components: {
    Swiper,
    SwiperSlide,
      
  },
  mounted () {
    
  },
  computed: {
    currentComponent () {
      return this.togglerMenu.find(i => i.isActive).route
    }
  },
  methods: {
    toggleMenu (item) {
      for(let i = 0; i < this.togglerMenu.length; i++) {
        this.togglerMenu[i].isActive = false
      }
      item.isActive = true
    },
    back () {
      this.$router.push("/payment/school/parameters")
    }
  }
}
</script>
 
<style lang="scss" scoped>
  .choosing-container {
    padding: 40px;
    box-sizing: border-box;
    min-height: calc(100vh - 64px);
    height: 100%;
    display: flex;
    flex-direction: column;
    & * {
      box-sizing: border-box;
    }
    @media screen and (max-width: 1500px) {
      & {
        padding: 24px 24px 24px 24px;
      }
    }
    @media screen and (max-height: 960px) {
      & {
        padding: 24px;
      }
    }
  }
  .choosing-caption {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    &-title {
      margin: 0px 10px 0px 20px;
      font-family: 'Vox-Mit';
      color: #404151;
      font-size: 2.77rem;
      & span {
        color: #7b7c8f;
      }
    }
    & .choosing-btns {
      margin-left: auto;
      display: flex;
      align-items: center;
      & .toggler {
        width: 170px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 6px;
        background: #7b7c8f;
        padding: 4px 0px;
        margin: 0px 20px 0px -4px;
        &-item {
          height: 32px;
          width: 80px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #fff;
          font-family: 'SSans-Reg';
          font-size: 1rem;
          margin: 0px 4px;
          cursor: pointer;
          user-select: none;
          &.active {
            background: #fff;
            border-radius: 6px;
            color: #7b7c8f;
          }
        }
      }
    }
    & .back {
      padding: 12px 16px;
      background: #fff;
      color: #7b7c90;
      font-family: 'SSans-Reg';
      font-size: 1rem;
      border: none;
      box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
      border-radius: 6px;
      cursor: pointer;
      user-select: none;
      height: 40px;
      & img {
        margin-right: 8px;
        margin-bottom: -3px;
      }
    }
  }
  .swiper-slides {
    position: relative;
    width: 100%;
    margin-top: 21px;
    // margin: 25px 0px 25px 0px;
    margin: auto 0px;
    & .swiper {
      padding-bottom: 40px;
      & .swiper-scrollbar {
        background: #d9dae2;
        height: 2px;
      }
    }
  }
  .right-arrow {
    height: 80px;
    width: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    right: -15px;
    transform: translate(0, -50%);
    border-radius: 6px;
    background: #fff;
    cursor: pointer;
    user-select: none;
    box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
    z-index: 77;
    outline: none;
    opacity: 1;
    transition-duration: .3s;
    &.swiper-button-disabled {
      opacity: 0;
      cursor: default;
    }
  }
  .left-arrow {
    height: 80px;
    width: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: -15px;
    transform: translate(0, -50%);
    border-radius: 6px;
    background: #fff;
    cursor: pointer;
    user-select: none;
    box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
    z-index: 77;
    outline: none;
    opacity: 1;
    transition-duration: .3s;
    &.swiper-button-disabled {
      opacity: 0;
      cursor: default;
    }
    & img {
      transform: rotate(180deg);
    }
  }
  .card {
    background: #fff;
    box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
    border-radius: 8px;
    user-select: none;
    // margin: 4px;
    &-main {
      padding: 40px 40px 24px 40px;
      text-align: center;
      border-bottom: 1px solid #dddfeb;
      &-title {
        color: #404151;
        font-family: 'Vox-Mit';
        font-size: 1.6rem;
        margin: 0px;
      }
      &-adress {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 20px 0px;
        & img {
          margin-right: 8px;
        }
        &-text {
          margin: 0px;
          color: #7b7c8f;
          font-family: 'SSans-Reg';
          font-size: 1rem;
        }
      }
      &-price {
        font-family: 'Vox-Mit';
        font-size: 1.33rem;
        color: #404151;
        margin: 0px;
        & span {
          color: #7b7c8f;
        }
      }
    }
    &-btn {
      padding: 16px;
      &-item {
        background: linear-gradient(to bottom, rgb(255, 111, 47), #f64628);
        padding: 15px 30px;
        border-radius: 25px;
        border: none;
        outline: none;
        color: #fff;
        font-family: 'SSans-SB';
        font-size: 1.2rem;
        cursor: pointer;
        box-shadow: 0px 5px 11px 0px rgba(255, 111, 47, 0.3);
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
      }
    }
  }
  .btns {
    display: flex;
  }
  .back-btn {
    background: #81909c;
    padding: 15px 30px;
    border-radius: 25px;
    border: none;
    outline: none;
    color: #fff;
    font-family: 'SSans-SB';
    font-size: 1.2rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-right: 20px;
    width: auto;
    & img {
      margin-right: 10px;
    }
  }
  .wrapper {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: space-between;
    & .map {
      width: 100%;
      height: 100%;
