<script>
import ClickOutside from 'vue-click-outside'
export default {
  name: "card-schedule",
  directives: {
    ClickOutside
  },
  data: () => ({
    schedule: {
      isOpen: false,
      items: ['Август 2019', 'Июль 2019', 'Июнь 2019', 'Май 2019', 'Апрель 2019', 'Март 2019']
    },
    scheduleInfo: {
      isOpen: true,
      items: [
        {
          date: 28,
          events: [
            {
              type: 'third',
              title: 'Экзамен ГИБДД',
              time: '9:00'
            },
            {
              type: 'fourth',
              title: 'Медицина',
              time: '12:30'
            },
            {
              type: 'fifth',
              title: 'Внутренний экзамен',
              time: '14:00'
            },
          ]
        }
      ]
    }
  }), 
  methods: {
    sheduleToggle() {
      this.schedule.isOpen = !this.schedule.isOpen
    },
    sheduleHide() {
      this.schedule.isOpen ? this.schedule.isOpen = false : false
    },
    openScheduleInfo() {
      this.scheduleInfo.isOpen = !this.scheduleInfo.isOpen
    }
  }
}
</script>

<style lang="scss">
  .cardSchedule {
