      align-items: center;
      padding: 8px 14px;
      user-select: none;
      cursor: pointer;
      transition-duration: .2s;
      white-space: nowrap;
      @media screen and (max-width: 1500px) {
        padding: 6px 12px;
      }
      & .title {
