<template>
  <div v-click-outside="closeDropDownMenu" :class="isOpen ? 'active' : ''" class="dropdown">
    <div @click="isOpen = !isOpen" class="current-value">
      <p class="current-value-title">{{ currentSection }}</p>
      <div class="current-value-dots">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
    <div :style="isOpen ? `max-height:${sections.length * 51}px` : ''" :class="isOpen ? 'active' : ''" class="items">
      <p @click="chooseSection(item)" v-for="item in sections" :key="item" class="item">{{ item }}</p>
    </div>
  </div>
</template>

<script>
import ClickOutside from 'vue-click-outside'
export default {
  name: 'dropdown-component',
  props: ['menu'],
  directives: {
    ClickOutside
  },
  data: () => ({
    currentValue: null,
    isOpen: false
  }),
  methods: {
    chooseSection (i) {
      this.currentValue = i
      this.isOpen = false
      this.$emit("onSelect", this.currentValue)
    },
    closeDropDownMenu () {
      this.isOpen = false
    }
  },
  computed: {
    currentSection () {
      return this.currentValue ? this.currentValue : this.menu[0]
    },
    sections () {
      return this.menu.filter(i => i !== this.currentSection)
    }
  },
  watch: {
    menu: {
      handler: function () {
        this.currentValue = null
        this.$emit("onSelect", this.currentSection)
      },
      deep: true
    }
  }
}
</script>

<style lang="scss" scoped>
  .dropdown {
    background: #fff;
    border: 1px solid #dddfeb;
    max-width: 280px;
    min-width: 280px;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    border-radius: 25px;
    user-select: none;
    cursor: pointer;
    &.active {
      border-bottom: none;
      border-bottom-left-radius: 0px;
      border-bottom-right-radius: 0px;
      margin-bottom: 1px;
      & .current-value {
        &-title {
          color: #404151;
        }
      }
    }
    & * {
      box-sizing: border-box;
    }
    & .current-value {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 15px 20px;
      &-title {
        margin: 0px;
        color: #7b7c90;
        font-family: 'SSans-Reg';
        font-size: 1.2rem;
        transition-duration: .2s;
      }
      &-dots {
        display: flex;
        align-items: center;
        & span {
          width: 2px;
          height: 2px;
          margin: 0px 1px;
          background: #7b7c8f;
        }
      }
    }
    & .items {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: inherit;
      max-height: 0px;
      transition: max-height .2s;
      overflow: hidden;
      position: absolute;
      transform: translate3d(0px, 50px, 0px);
      will-change: transform;
      border: 1px solid transparent;
      &.active {
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;
        border-color: #dddfeb;
        border-top: none;
        height: 100%;
        background: #fff;
        & .item {
          border-top: 1px solid #dddfeb;
          opacity: 1;
          &:hover {
            background: rgba(0,0,0, 0.05);
          }
        }
      }
      & .item {
        margin: 0px;
        padding: 15px 20px;
        width: 100%;
        color: #7b7c90;
        font-family: 'SSans-Reg';
        font-size: 1.2rem;
        transition: background .2s;
        opacity: 0;
        &:last-child {
          margin-bottom: 0px;
        }
      }
    }
  }
</style>