<template>
  <div class="onlineCourses">
      
    <div class="onlineCourses-container">
      <div class="onlineCourses-caption">
        <p class="onlineCourses-caption-title">Выберите курс</p>
        <button @click="$router.push('/payment')" class="back"><img ondragstart="return false" src="../assets/payment/close.png" alt="">Отменить</button>
      </div>
      <div class="onlineCourses-main">
        <div class="onlineCourses-main-band">
          <div @click="toggleCategory(c)" v-for="c in categories" :key="c.title" :class="c.isCurrent ? 'current' : ''" class="onlineCourses-main-band-item">
            <div class="onlineCourses-main-band-item-caption">
              <div class="onlineCourses-main-band-item-caption-item">
                <img class="onlineCourses-main-band-item-caption-item-icon" ondragstart="return false" :src="require(`../assets/payment/Payment_${c.icon}_${c.isCurrent ? 'R' : 'G'}.svg`)" alt="">
                <p class="onlineCourses-main-band-item-caption-item-title">{{ c.title }}</p>
              </div>
            </div>
            <div v-for="r in c.rates" :key="r.title" class="onlineCourses-main-band-item-rate">
              <div class="onlineCourses-main-band-item-rate-item">
                <p class="onlineCourses-main-band-item-rate-item-desc">{{ r.title }}</p>
                <p class="onlineCourses-main-band-item-rate-item-title">{{ r.price }} <span>Р</span></p>
              </div>
            </div>
          </div>
        </div>
        <div class="onlineCourses-main-payment">
          <p class="onlineCourses-main-payment-title">Оформление покупки: <span>{{ currentCategory.title }}</span></p>
          <div class="onlineCourses-main-payment-main">
            <div class="onlineCourses-main-payment-main-dropdowns">
              <div class="onlineCourses-main-payment-main-dropdowns-item">
                <p class="onlineCourses-main-payment-main-dropdowns-item-title">Выберите срок обучения</p>
                <dropdown @onSelect="onSelectMenu" :menu="categoryMenu"></dropdown>
              </div>
              <div class="onlineCourses-main-payment-main-dropdowns-item">
                <p class="onlineCourses-main-payment-main-dropdowns-item-title">Выберите срок обучения</p>
                <dropdown  @onSelect="onSelectTypes" :menu="categoryTypes"></dropdown>
              </div>
              <div class="onlineCourses-main-payment-main-dropdowns-item">
                <p class="onlineCourses-main-payment-main-dropdowns-item-title">Введите промо-код</p>
                <div class="onlineCourses-main-payment-main-dropdowns-item-input">
                  <inputComponent @onInput="onInput"></inputComponent>
                  <div class="onlineCourses-main-payment-main-dropdowns-item-input-accept">
                    <img ondragstart="return false" src="../assets/payment/accept.png" alt="">
                    <p class="onlineCourses-main-payment-main-dropdowns-item-input-accept-text">Применить</p>
                  </div>
                </div>
              </div>
            </div>
            <div class="onlineCourses-main-payment-main-info">
              <span class="btn-info">i</span>
              <p class="text">При выборе режима обучения “Открыть все уроки” у вас будет доступ ко всем урокам. Вы сможете просматривать их в любом порядке. При выборе «Пошаговое обучение» у вас будет открыт только один урок. Чтобы получить доступ к следующему, вам будет необходимо просмотреть урок полностью от начала до конца, а также сдать зачет по пройденной теме (при просмотре урока в первый раз перемотка не будет работать). </p>
            </div>
            <div class="onlineCourses-main-payment-main-prices-wrapper">
              <div class="onlineCourses-main-payment-main-price">
                <div class="onlineCourses-main-payment-main-price-item">
                  <p class="onlineCourses-main-payment-main-price-item-title">Стоимость:</p>
                  <p class="onlineCourses-main-payment-main-price-item-cost">300 <span>Р</span></p>
                </div>
                <div class="onlineCourses-main-payment-main-price-item">
                  <p class="onlineCourses-main-payment-main-price-item-title">Скидка:</p>
                  <p class="onlineCourses-main-payment-main-price-item-cost">-100 <span>Р</span></p>
                </div>
                <div class="onlineCourses-main-payment-main-price-item result">
                  <p class="onlineCourses-main-payment-main-price-item-title">Итого к оплате:</p>
                  <p class="onlineCourses-main-payment-main-price-item-cost">200 <span>Р</span></p>
                </div>
              </div>
              <button @click="toPay" class="prices-btn">
                <img ondragstart="return false" src="../assets/payment/card-icon.png" alt="">  
                Оплатить
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
 
import dropdown from "@/components/desktop/dropdown-component"
import inputComponent from "@/components/desktop/input-component"
export default {
  name: 'onlineCourses',
  data: () => ({
    categories: [
      {
        title: "Категория A",
        rates: [
          {
            title: "1 месяц",
            price: 100,
          },
          {
            title: "2 месяца",
            price: 300,
          },
          {
            title: "1 год",
            price: 1000,
          },
        ],
        isCurrent: true,
        icon: 'A'
      },
      {
        title: "Категория B",
        rates: [
          {
            title: "1 месяц",
            price: 200,
          },
          {
            title: "2 месяца",
            price: 300,
          },
          {
            title: "1 год",
            price: 1000,
          },
        ],
        isCurrent: false,
        icon: 'B'
      },
      {
        title: "Категория C",
        rates: [
          {
            title: "1 месяц",
            price: 200,
          },
          {
            title: "2 месяца",
            price: 300,
          },
          {
            title: "1 год",
            price: 1000,
          },
        ],
        isCurrent: false,
        icon: 'C'
      },
      {
        title: "Категория A+B+C",
        rates: [
          {
            title: "1 месяц",
            price: 400,
          },
          {
            title: "2 месяца",
            price: 600,
          },
          {
            title: "1 год",
            price: 2000,
          },
        ],
        isCurrent: false,
        icon: 'ABC'
      },
    ],
    promoValue: '',
    selectedMenu: '',
    selectedTypes: ''
  }),
  components: {
    dropdown, inputComponent
  },
  methods: {
    toggleCategory (c) {
      for(let i = 0; i < this.categories.length; i++) {
        this.categories[i].isCurrent = false
      }
      c.isCurrent = true
    },
    onInput(e) {
      this.promoValue = e
    },
    onSelectMenu (e) {
      this.selectedMenu = e
    },
    onSelectTypes (e) {
      this.selectedTypes = e
    },
    toPay () {
      //
      this.$router.push('/payment/agreement')
    }
  },
  computed: {
    currentCategory() {
      return this.categories.find(c => c.isCurrent)
    },
    categoryTypes () {
      return ['Открыть все уроки', 'Пошаговое обучение']
    },
    categoryMenu() {
      let rates = this.categories.find(c => c.isCurrent).rates
      let ratesRendered = []
      for(let i = 0; i < rates.length; i++) {
        ratesRendered.push(`${rates[i].title} за ${rates[i].price} руб.`)
      }
      return ratesRendered
    }
  }
}
</script>
 
