    flex-direction: column;
    &-band {
      display: flex;
      align-items: center;
      margin: 0px -4px; 
      &-item {
        cursor: pointer;
        width: calc((100% / 4) - 4px);
        margin: 25px 4px;
        box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
        border-radius: 8px;
        user-select: none;
        background: #fff;
        &-caption {
          height: 100%;
          min-height: 150px;
          display: flex;
          flex-direction: column;
