              margin: 0px;
              width: 75px;
              color: #7b7c90;
              font-family: 'SSans-Reg';
              font-size: 1rem;
            }
            &-title {
              margin: 0px;
              font-size: 1.33rem;
              width: 75px;
              font-family: 'Vox-Mit';
              color: #404151;
              & span {
                color: #7b7c8f;
              }
            }
          }
        }
        &.current {
          border-top: 2px solid #f44b2f;
          border-bottom: 2px solid #f44b2f;
          & .onlineCourses-main-band-item-caption-item-title {
            color: #f44b2f;
          }
        }
      }
    }
    &-payment {
      flex: 1;
      display: flex;
      flex-direction: column;
      user-select: none;
      &-title {
        font-family: 'Vox-Mit';
        color: #404151;
        font-size: 1.33rem;
        margin: 0px;
        & span {
          color: #7b7c8f;
        }
      }
      &-main {
        margin-top: 20px;
        padding: 40px 16px;
        box-shadow: 0px 5px 11px 0px rgb(230, 231, 238);
        background: #fff;
        border-radius: 8px;
        // flex: 1;
        // display: flex;
        // flex-direction: column;
        // justify-content: space-between;
        &-dropdowns {
          display: flex;
          align-items: flex-start;
          margin: 0px 10px 20px 10px;
          &-item {
            margin: 0px 10px;
            &-title {
              color: #404151;
              font-family: 'SSans-Reg';
              font-size: 1rem;
              margin: 0px 0px 10px 20px; 
            }
            &-input {
              display: flex;
