              &-accept {
                display: flex;
                align-items: center;
                cursor: pointer;
                margin-left: 20px;
                user-select: none;
                & img {
                  margin-right: 6px;
                  margin-top: 3px;
                }
                &-text {
                  color: #f44b2f;
                  font-family: 'SSans-Reg';
                  font-size: 1rem;
                  margin: 0px;
                }
              }
            }
          }
        }
        &-info {
          background: #f0f1f7;
          border-radius: 8px;
          width: 100%;
          padding: 15px;
          display: flex;
          align-items: flex-start;
          & .text {
            color: #404151;
            font-family: 'SSans-Reg';
            font-size: 1rem;
            margin: 0px;
          }
          & .btn-info {
            border-radius: 50%;
            max-width: 16px;
            width: 100%;
            height: 16px;
            display: flex;
            flex-direction: column;
