            justify-content: center;
            color: #fff;
            font-family: 'SSans-SB';
            cursor: pointer;
            font-size: 1rem;
            background: linear-gradient(to bottom, #ff6f2f, #f64628);
            padding-bottom: 1px;
            margin-right: 8px;
          }
        }
        &-prices-wrapper {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 100%;
          margin-top: 20px;
          & .prices-btn {
            background: linear-gradient(to bottom, rgb(255, 111, 47), #f64628);
            padding: 15px 30px;
